@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;
.Registro {
    background-color: v.$Cafe;
    color: v.$ColorFondo;
    padding: 40px;
    margin: 50px;
    border-radius: 30px;
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto 1fr; 
  
    h2 {
      color: v.$ColorFondo;
      text-align: center;
      grid-row: 1;
      grid-column: 1;
    }
  
    .datos {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 20px;
      padding: 10px;
    }
  
    .form-group {
      width: 100%;
    }
  
    .ultimo {
        margin-bottom: 0;       // o un valor menor si deseas un poco de separacin
        width: 50%;             // ajusta segn el ancho que desees
        grid-column: span 2;    // abarca las dos columnas
        justify-self: center;   // centra horizontalmente
        align-self: center;     // centra verticalmente (si aplica en tu grid)
      }
      
      
    
    input, select {
      width: 100%;
      padding: 10px;
      margin-top: 5px;
      border-radius: 5px;
      border: 1px solid #ccc;
    }
  
    // Nueva fila para confirmar contrasea y checkbox
    .row-doble {
      grid-column: span 2;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 20px;
    }
  
    .checkbox-group {
      display: flex;
      align-items: center;
      // Aseguramos que solo ocupe la segunda columna (no se hace span)
      input[type="checkbox"] {
        width: auto;
        margin-right: 10px;
      }
      label {
        margin: 0;
        font-size: 1.4rem; // Tamao de letra aumentado
      }
      a {
        color: v.$ColorFondo;
        text-decoration: underline;
      }
    }
  
    .boton-enviar {
      grid-column: span 2;
      padding: 10px;
      margin-top: 20px;
      background-color: v.$ColorFondo;
      color: v.$Cafe;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      text-align: right;
      width: 100px;
      justify-self: end;
      transition: background-color .5s;
      &:hover {
        background-color: v.$amarillo;
      }
    }
  }
  
  .RegistroExitoso {
    h3 {
      margin: 0 auto;
      padding: 1% 0;
      border-radius: 20px;
      width: 50%;
      background-color: v.$VerdeExito;
      color: v.$CafeUbicaciones;
    }
    p {
      text-align: center;
      color: v.$Cafe;
      font-size: 2vw;
      font-family: v.$Texto_letra_italica;
      font-weight: 900;
    }
  }
  
  p.ErrorRegistro {
    color: v.$blanco;
    background-color: v.$Rojo;
    text-align: center;
    margin: .5% 20%;
    border-radius: 10px;
    font-size: 1.5vw;
  }
  
  .Contenedor_contrasena {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .Contenedor_contrasena input {
    flex: 1;
  }
  
  .toggle-password {
    position: absolute;
    right: 1%;
    top: 12%;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 3.5rem;
  }


  