@use 'Base/Variables' as v;
@use 'Base/mixins' as m;


// Mixins
@mixin transicion-suave {
    transition: all 0.3s ease;
}

// Estilos principales - sin fondos
.contenedor-insignias {
    width: 100%; // Ocupar todo el ancho disponible
    margin: 2rem 0; // Quitar margen horizontal
    padding: 0; // Quitar padding horizontal
    background: transparent;
}

// Seccin 1: Insignias Totales - Sin fondo, solo imagen
.seccion-insignias-totales {
    margin-bottom: 3rem;
    text-align: center;
    background: transparent;

    .insignia-totales-img {
        width: 96%;
        max-height: 80rem;
        object-fit: contain;
        margin: 0 2%; // Margen del 2% a cada lado
        background: transparent;
    }
}

// Seccin 2: Insignias del Mes - Sin fondo, solo imagen
.seccion-insignias-mes {
    margin-bottom: 3rem;
    text-align: center;
    background: transparent;

    .insignia-mes-img {
        width: 40%;
        max-height: 300px;
        object-fit: contain;
        display: block;
        margin: 0 auto;
        background: transparent;
    }
}

// Seccin 3: Lnea de Progresin - Sin fondo, solo imgenes y lnea
.seccion-progresion {
    padding: 1rem 0;
    margin-bottom: 2rem;
    width: 100%; // Asegurar que ocupe todo el ancho
    background: transparent;
    position: relative; // Para posicionar correctamente la lnea

    .titulo-progresion {
        color: v.$VerdeUb;
        text-align: center;
    }

    .linea-progresion {
        display: flex;
        flex-wrap: nowrap; // Forzar que todas estn en una lnea
        justify-content: space-between; // Distribuir a lo largo de todo el ancho
        gap: 0; // Eliminar gap, el space-between se encarga de la distribucin
        position: relative;
        width: 98%; // Casi todo el ancho
        margin: 0 1%; // Centrar con pequeo margen

        // Lnea de conexin azul completa
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 6%; // Comenzar al principio de la primera imagen
            right: 6%; // Terminar al principio de la ltima imagen
            height: 1rem;
            background: v.$Verde; // Color azul slido (sin degradado)
            z-index: 1;
        }
        
    }

    .insignia-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;
        background: transparent;
        padding: 0; // Eliminar padding
        width: 12%; // Ancho proporcional (8 insignias aproximadamente)
        min-width: 80px; // Ancho mnimo para mviles
        @include transicion-suave;

        &:hover {
            transform: translateY(-5px);
        }

        .insignia-contenedor {
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .insignia-imagen {
            width: 100%; // Que ocupe todo el ancho del contenedor
            height: auto; // Mantener proporcin
            max-width: 15rem; // Tamao mximo
            max-height: 15rem;
            object-fit: contain;
            @include transicion-suave;

            &.desbloqueada {
                filter: none;
            }

            &.bloqueada {
                background-color: transparent;
            }
            
            &.nueva-insignia {
                animation: destello 2s ease-in-out 1;
                z-index: 5;
            }
        }

        // Ocultar todos los textos
        .insignia-descripcion,
        .insignia-animal,
        .insignia-requisito {
            display: none;
        }
    }
}

// Animacin para nuevas insignias
@keyframes destello {
    0% {
        transform: scale(1);
        filter: brightness(1);
    }
    25% {
        transform: scale(1.3);
        filter: brightness(1.5) drop-shadow(0 0 10px v.$Verde);
    }
    50% {
        transform: scale(1.2);
        filter: brightness(1.3) drop-shadow(0 0 15px v.$Verde);
    }
    75% {
        transform: scale(1.1);
        filter: brightness(1.2) drop-shadow(0 0 5px v.$Verde);
    }
    100% {
        transform: scale(1);
        filter: brightness(1);
    }
}

// Contenedor de confeti
.nueva-insignia-contenedor {
    position: relative;
    display: inline-block;
}

// Estilos para el confeti
.confeti {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    pointer-events: none;
    z-index: 3;
}

.confeti-pieza {
    position: absolute;
    width: 8px;
    height: 16px;
    background-color: #f00;
    top: -10px;
    opacity: 0;
    animation: caida-confeti 3s ease-in-out forwards;
    z-index: 4;
}

@keyframes caida-confeti {
    0% {
        opacity: 1;
        top: 0;
        transform: translateY(0) rotate(0deg);
    }
    100% {
        opacity: 0;
        top: 100%;
        transform: translateY(1000%) rotate(720deg);
    }
}

// Imagen fallback - mantenerla por si falta alguna imagen
.insignia-placeholder {
    width: 8rem;
    height: 8rem;
    background-color: transparent;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #666;
    font-weight: bold;
}

// Responsive - ajustado para mantener distribucin en pantallas pequeas
@media (max-width: 1024px) {
    .seccion-progresion .linea-progresion {
        flex-wrap: wrap; // Permitir envoltura en pantallas medianas
        justify-content: space-around; // Distribuir con espacio alrededor
        
        // Modificar la lnea para pantallas ms pequeas
        &::before {
            left: 10%; // Ajustar para pantallas ms pequeas
            right: 10%;
        }
    }
    
    .insignia-item {
        width: 20% !important; // 5 por fila
        margin-bottom: 1rem;
    }
}

@media (max-width: 768px) {
    .seccion-insignias-mes .insignia-mes-img {
        width: 60%;
    }
    
    .insignia-item {
        width: 25% !important; // 4 por fila
    }
    
    .seccion-progresion .linea-progresion::before {
        left: 12%;
        right: 12%;
    }
}

@media (max-width: 480px) {
    .seccion-insignias-mes .insignia-mes-img {
        width: 80%;
    }
    
    .insignia-item {
        width: 33.33% !important; // 3 por fila
    }
    
    .seccion-progresion .linea-progresion::before {
        left: 15%;
        right: 15%;
    }
}


/* Estilos para la seccin de insignias mensuales - fondo completo */
.seccion-insignias-especiales {
    overflow: visible;
    position: relative;
    width: 100%;
    padding: 0;
    margin: 5% 0;
    
    /* Ajustes para mostrar la imagen de fondo completa */
    background-image: url('/vectores/User/Insignias/ILUSTRACION INSIGNIAS.svg');
    background-size: contain; 
    background-position:bottom;
    background-repeat: no-repeat;    
    /* Ajustes de alineacin */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: -1em;
    @include m.tablet{
        padding-bottom: 20%;
    }
}

.insignias-especiales-titulo {
    font-size: 5rem;
    color: v.$Cafe; /* Fallback por si la variable no est definida */
    margin-bottom: 2rem;
    margin-top: 1rem; /* Aadido espacio arriba */
    text-align: center;
}

.insignias-especiales-contenido {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 2.5rem;
    border-radius: 12px;
    margin-bottom: 0;
    
}

.insignias-especiales-contador {
    font-size: 5rem;
    font-weight: bold;
    color: v.$VerdeUb;
    margin-right: 1rem;
}

.insignia-especial-contenedor {
    position: relative;
    display: inline-block;
}

.insignias-especiales-imagen {
    width: 15rem;
    height: 15rem;
    object-fit: contain;
    transition: all 0.3s ease;
    
    // Estado inicial para nuevas insignias especiales (sin animacin hasta que se vea)
    &.nueva-insignia-especial-pendiente {
        // Mantener el estado normal hasta que se active la animacin
        filter: brightness(1);
        transform: scale(1);
    }
    
    // Animacin que se activa cuando la seccin es visible
    &.nueva-insignia-especial {
        animation: destello-especial 3s ease-in-out 1;
        z-index: 5;
    }
}

// Mejorar la transicin de desbloqueo
.insignia-imagen {
    transition: opacity 0.3s ease, transform 0.3s ease;
    
    &.nueva-insignia {
        animation: destello-secuencial 2s ease-in-out 1;
        z-index: 5;
    }
}

// Animacin especfica para desbloqueo secuencial
@keyframes destello-secuencial {
    0% {
        transform: scale(1);
        filter: brightness(1);
    }
    20% {
        transform: scale(1.1);
        filter: brightness(1.2) drop-shadow(0 0 5px v.$Verde);
    }
    40% {
        transform: scale(1.3);
        filter: brightness(1.5) drop-shadow(0 0 15px v.$Verde);
    }
    60% {
        transform: scale(1.2);
        filter: brightness(1.4) drop-shadow(0 0 12px v.$Verde);
    }
    80% {
        transform: scale(1.1);
        filter: brightness(1.2) drop-shadow(0 0 8px v.$Verde);
    }
    100% {
        transform: scale(1);
        filter: brightness(1);
    }
}

// Animacin especfica para insignias especiales (ms dramtica y con oro)
@keyframes destello-especial {
    0% {
        transform: scale(1) rotate(0deg);
        filter: brightness(1);
    }
    15% {
        transform: scale(1.2) rotate(5deg);
        filter: brightness(1.3) drop-shadow(0 0 10px gold);
    }
    30% {
        transform: scale(1.5) rotate(-10deg);
        filter: brightness(1.6) drop-shadow(0 0 25px gold);
    }
    45% {
        transform: scale(1.3) rotate(8deg);
        filter: brightness(1.4) drop-shadow(0 0 20px gold);
    }
    60% {
        transform: scale(1.6) rotate(-5deg);
        filter: brightness(1.8) drop-shadow(0 0 35px gold);
    }
    75% {
        transform: scale(1.2) rotate(3deg);
        filter: brightness(1.3) drop-shadow(0 0 15px gold);
    }
    90% {
        transform: scale(1.1) rotate(-2deg);
        filter: brightness(1.2) drop-shadow(0 0 8px gold);
    }
    100% {
        transform: scale(1) rotate(0deg);
        filter: brightness(1);
    }
}

// Contenedor especfico para confeti de insignias especiales
.nueva-insignia-especial-contenedor {
    position: relative;
    display: inline-block;
}

// Confeti especial para insignias especiales
.confeti-especial {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    pointer-events: none;
    z-index: 3;
}

.confeti-especial-pieza {
    position: absolute;
    width: 12px;
    height: 20px;
    background-color: #FFD700;
    top: -15px;
    opacity: 0;
    animation: caida-confeti-especial 4s ease-in-out forwards;
    z-index: 4;
    border-radius: 2px;
}

@keyframes caida-confeti-especial {
    0% {
        opacity: 1;
        top: 0;
        transform: translateY(0) rotate(0deg) scale(1);
    }
    25% {
        opacity: 1;
        transform: translateY(100px) rotate(180deg) scale(1.2);
    }
    50% {
        opacity: 0.8;
        transform: translateY(300px) rotate(360deg) scale(0.9);
    }
    75% {
        opacity: 0.5;
        transform: translateY(600px) rotate(540deg) scale(1.1);
    }
    100% {
        opacity: 0;
        top: 100%;
        transform: translateY(1200%) rotate(720deg) scale(0.8);
    }
}

/* Media queries para adaptabilidad */
@media (max-width: 1024px) {
    .seccion-insignias-especiales {
        padding-bottom: 25vw;
        background-size: 100% auto; /* Ajuste alternativo para pantallas medianas */
    }
}

@media (max-width: 768px) {
    .seccion-insignias-especiales {
        padding: 3rem 1rem;
        padding-bottom: 18rem;
        margin-top: -5vw;
    }
    
    .insignias-especiales-titulo {
        font-size: 3rem;
    }
    
    .insignias-especiales-contador {
        font-size: 2.5rem;
    }
    
    .insignias-especiales-imagen {
        width: 90px;
        height: 90px;
    }
}

@media (max-width: 480px) {
    .seccion-insignias-especiales {
        padding: 3rem 1rem;
        padding-bottom: 10rem;
        margin-top: -15vw;
    }
    
    .insignias-especiales-titulo {
        font-size: 3rem;
    }
    
    .insignias-especiales-contenido {
        flex-direction: column;
    }
    
    .insignias-especiales-contador {
        font-size: 2rem;
        margin-right: 0;
    }
}
