@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

.nos {
    .quienessomos {
        @include m.desktop{
            margin-top: -5%;
        }
        .contenido {
            display: flex;
            flex-direction: column;

            @include m.desktop {
                flex-direction: row;
                align-items: center;
            }

            .texto {
                order: 2;
                width: 100%;
                padding: 1rem;

                @include m.desktop {
                    order: 1;
                    padding: 2rem;
                }

                h1 {
                    color: v.$CafeUbicaciones;
                    font-size: 3.5rem;
                    font-weight: 900;
                    text-align: center;
                    margin-top: -8rem;
                    margin-bottom: -1rem;
                    @include m.tablet {
                        font-size: 5rem;
                    }
                    @include m.desktop {
                        font-size: 3.5em;
                        color: v.$VerdeTitUb;
                        font-family: v.$Texto_titulos;
                        
                    }
                }

                .logo {
                    display: block;
                    width: 70%;
                    margin: auto;    
                    @include m.tablet {
                        width: 50%;
                    }
                    @include m.desktop {
                        width: 72%;
                    }
                }

                p {
                    font-size: 1.8rem;
                    line-height: 1.2;
                    color: v.$CafeUbicaciones;
                    text-align: justify;
                    width: 85%;
                    margin: auto;
                    margin-bottom: 2rem;
                    @include m.tablet {
                        font-size: 2rem;
                    }
                    @include m.desktop {
                        font-size: 1.3em;
                        margin: 0 auto;
                        width: 85%;
                        margin-top: 5%;
                    }
                }
            }

            .imagen-principal {
                position: relative;
                order: 1;
                width: 100%;
                overflow: hidden;
                
                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 1;
                    
                    /* Prefijos para Safari y navegadores antiguos */
                    background: -webkit-linear-gradient(top, rgba(55, 214, 55, 0.6) 0%,
                                                 rgba(55, 214, 55, .5) 20%,
                                                 rgba(55, 214, 55, 0.2) 60%,
                                                 rgba(0, 255, 0, 0) 90%);
                    background: -moz-linear-gradient(top, rgba(55, 214, 55, 0.6) 0%,
                                              rgba(55, 214, 55, .5) 20%,
                                              rgba(55, 214, 55, 0.2) 60%,
                                              rgba(0, 255, 0, 0) 90%);
                    /* Versin estndar al final */
                    background: linear-gradient(to bottom, rgba(55, 214, 55, 0.6) 0%,
                                           rgba(55, 214, 55, .5) 20%,
                                           rgba(55, 214, 55, 0.2) 60%,
                                           rgba(0, 255, 0, 0) 90%);
                }
            
                img {
                    /* Prefijos para mask-image en Safari */
                    -webkit-mask-image: -webkit-linear-gradient(top, rgba(0, 0, 0, .7) 5%, 
                                                     rgba(0, 0, 0, .5) 25%,
                                                     rgba(0, 0, 0, 0) 85%);
                    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, .7) 5%, 
                                          rgba(0, 0, 0, .5) 25%,
                                          rgba(0, 0, 0, 0) 85%);
                    object-fit: cover;
                    height: 35rem;
                    object-position: 32% 32%;
                    @include m.tablet {
                        height: 55rem;
                    }
                    @include m.desktop {
                        height: auto;
                        z-index: -2;
                    }
                }
                
                @include m.desktop {
                    order: 2;
                    width: 85%;
                    &::before{
                        display: none;
                    }
                    img {
                        -webkit-mask-image: none; // Elimina el degradado de la imagen en desktop
                        mask-image: none; // Elimina el degradado de la imagen en desktop
                    }
                }
            }
        }
    }

    .seccion-info{
        margin-bottom: 20%;
        position : relative;
        h1{
            letter-spacing: 0;
            color: v.$blanco;
            font-size: 3.5vw;
            width: 85%;
            margin: auto;
            font-family: v.$Texto_titulos_secundarios;
            text-align: center;
            position: absolute;
            top: 10%;
            left: 10%;
            
            @include m.tablet {
                padding: 2%;
                font-size: 3vw;
            }
            @include m.desktop{
                font-size: 2.5vw;
            }
        }
        img.imagen-flor{
            position: absolute;
            right: 8%;
            bottom: -10%;
            width: 12%;
            height: auto;
        }
        .MisionCont{
            position: absolute;
            bottom: -25%;
            
            h2{
                color: v.$VerdeTitUb;
                margin-bottom: 2%;
                text-align: center;
                font-size:7vw;
                font-family: v.$Texto_titulos;
                letter-spacing: -1px;
                @include m.tablet {
                    font-size: 6vw;
                }
                @include m.desktop {
                    font-size: 6.5vw;
                    margin: 0;
                    margin-left: 10%;

                }
            }
            p{
                color: v.$CafeUbicaciones;
                width: 65%;
                margin: auto 12%;
                text-align: justify;
                font-size:3.5vw;
                @include m.tablet {
                    font-size: 3vw;
                }
                @include m.desktop {
                    font-size: 2vw;
                    margin: 0;
                    width: 50%;
                    text-align: justify;
                }
            }
            @include m.tablet {
                bottom: -10%;
            }
            @include m.desktop {
                display: flex;
                align-items: center;
                justify-content: space-around;
                width: 85%;
                bottom: 12%;
                margin: auto;
            }
        }
        @include m.desktop{
            margin-bottom: 2%;
        }
    }


    .somos_cadena {
        border-left: 3rem;

        h1 {
            color: v.$CafeUbicaciones;
            letter-spacing: 1px;
            text-align: center;
            font-size: 1.6em;
            @include m.tablet {
                margin-bottom: 2%;
                font-size: 4.5rem;
            }
            @include m.desktop {
                font-size: 3.5vw;
            }
        }

        img {
            width: 100%;
            height: auto;
            display: block;
        }

        // Estilos para las tablas de normas
        .tabla-normas {
            width: 85%;
            margin: 0 auto 3rem auto;
            border-collapse: separate;
            border-spacing: 0;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            overflow: hidden;
            tr {
                display: flex;
                flex-direction: column;

                @include m.tablet {
                    flex-direction: row;
                    height: auto;
                }
            }

            .celda-texto,
            .celda-texto2 {
                width: 100%;
                padding: 2rem 3rem;

                @include m.tablet {
                    width: 50%;
                    height: auto;
                }

                h1 {
                    color: white;
                    margin-bottom: 2rem;
                    text-align: left;
                    font-size: 3rem;
                    font-weight: 500;
                    @include m.desktop{
                        font-size: 3.5vw;
                    }
                }

                h2 {
                    color: white;
                    font-size: 2.5rem;
                    margin-bottom: 2rem;
                    font-weight: 200;
                    &.masespacio{
                        @include m.desktop{
                            margin-bottom: 8%;
                        }
                    }
                    @include m.desktop {
                        font-size: 2.5vw;
                        
                    }
                }

                p {
                    font-size: 2rem;
                    color: white;
                    text-align: justify;
                    margin-bottom: 0;
                    @include m.desktop {
                        font-size: 1.7vw;
                    }
                }
            }

            .celda-texto {
                background-color: rgb(133, 188, 75);
            }

            .celda-texto2 {
                background-color: rgb(35, 131, 76);
            }

            .celda-imagen {
                width: 100%;
                padding: 0;
                background-color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                order: -1; // La imagen va primero en mvil

                @include m.tablet {
                    width: 50%;
                    order: 0;
                    height: auto;
                }

                img {
                    width: 100%;
                    height: auto;
                    object-fit: cover;
                    border-radius: 0;
                }
            }
        }
    }

    // Nuevo cdigo para la seccin de colaboradores con carrusel
    .colaboradores {
        position: relative;
        margin: 2rem 0;
        text-align: center;
        overflow: hidden;
        padding-bottom: 5rem;

        h1 {
            color: v.$CafeUbicaciones;
            text-align: center;
            font-size: 3.5rem;
            margin-bottom: 3rem;
            @include m.tablet {
                font-size: 4.5rem;
            }
        }

        .fondo-colaboradores {
            width: 100%;
            height: auto;
            display: block;
            position: relative;
            z-index: 1;
        }

        .carrusel-container {
            position: relative;
            width: 100%;
            margin: 0 auto;
            padding: 2rem 0;
            z-index: 2;
            overflow: hidden; // Mantiene oculto lo que est fuera del contenedor
        }

        .carrusel-slides-wrapper {
            display: flex;
            width: 100%;
            position: relative;
            transition: transform 0.5s ease; // Transicin suave para el efecto de deslizamiento
        }

        .carrusel-slide {
            display: flex;
            justify-content: space-around;
            gap: 2rem;
            padding: 0 1rem;
            min-width: 100%; // Cada slide ocupa el 100% del ancho
            opacity: 0.7; // Ligera transparencia para slides no activos
            transition: opacity 0.5s ease;
            
            &.active {
                opacity: 1; // Slide activo totalmente visible
            }
        }

        .colaborador-item {
            box-shadow: 0px 0px 4px v.$VerdeUb;
            flex: 1;
            max-width: 30%;
            padding: 1rem;
            background-color: transparent;
            border-radius: 15px;
            
            img {
                width: 100%;
                height: auto;
                display: block;
                border-radius: 8px;
                transition: transform 0.3s ease;     
            }
            
            @include m.tablet {
                padding: 1.5rem;
                max-width: 25%;
            }
        }

        .carrusel-indicadores {
            display: flex;
            justify-content: center;
            margin-top: 2rem;
            gap: 1rem;

            .indicador {
                width: 12px;
                height: 12px;
                border-radius: 50%;
                background-color: rgba(104, 33, 10, 0.3);
                cursor: pointer;
                transition: background-color 0.3s ease;

                &.active {
                    background-color: rgb(104, 33, 10);
                }

                &:hover {
                    background-color: rgba(104, 33, 10, 0.7);
                }
            }
        }
    }
    
}

.cadena_gift_Nosotros{
        position: relative;
        width: 100%;
        margin: 4rem 0;
        margin-bottom: 0;
        overflow: hidden;

        // Imagen de fondo
        .fondo-svg {
            width: 100%;
            height: auto;
            object-fit: cover;
            object-position:top;
            display: block;
            position: relative;
            z-index: 1;
            @include m.tablet {
                height: 40vw;
            }
            @include m.desktop {
                height: 35vw;
            }
        }
       
        // Contenedor superpuesto que divide la seccin en dos partes
        .contenedor-superpuesto {
            display: flex;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            flex-direction: column;
            z-index: 2;

            @include m.tablet {
                display: flex;
                flex-direction: row;
            }

            // Lado izquierdo (imagen esttica)
            .lado-izquierdo {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 2rem 4rem;
                @include m.tablet {
                    width: 60%;
                    margin: auto;
                    display: flex;
                }
                @include m.desktop {
                    width: 55%;
                }

                .imagen-estatica {
                    object-fit: contain;
                    @include m.desktop {
                        width: 80%;
                    }
                    
                }
            }

            // Lado derecho (GIF animado)
            .lado-derecho {
                display: none;
                width: 100%;
                height: 50%;
                align-items: center;
                justify-content: center;
                padding: 2rem 4rem;

                @include m.tablet {
                    display: flex;
                    width: 50%;
                    margin: auto;
                }
                @include m.desktop {
                    width: 45%;
                }

                .gif-animado {
                    object-fit: contain;
                    height: 70rem;
                    @include m.desktop {
                        height: 50rem;
                    }
                }
            }
        }
    }

.FondoCamionesNosotros {
    background-image: url('/vectores/Nosotros/Fondo_nosotros_gifs.jpg');
    background-repeat: repeat-y; 
    background-size: 100% auto; 
    height: calc(15rem * 7);
    
    /* Mobile - 1 imagen por fila (vertical) */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    
    /* Tablet - 2 imgenes por fila (2x2) */
    @include m.tablet {
        height: auto;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        gap: 1rem;
        padding: 5% 0;
    }
    
    /* Desktop - todas las imgenes en una fila horizontal */
    @include m.desktop {
        height: 35rem;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-evenly;
        align-items: center;
        gap: 1rem;
    }
    
    /* Estilos para los artculos contenedores */
    article {
        /* Mobile - cada imagen ocupa el ancho disponible */
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        /* Tablet - cada imagen ocupa aproximadamente la mitad */
        @include m.tablet {
            width: 45%;
        }
        
        /* Desktop - cada imagen ocupa 1/4 del espacio */
        @include m.desktop {
            width: 18%;
        }
    }
    .escpecialCrifras{
        margin-bottom: -4.5rem;
        @include m.tablet{
        margin-bottom: 0;
        }
    }
    /* Estilos para las imgenes */
    .gif-cifras {
        width: 32rem;
        height: auto;
        position: relative;
        
        /* Tablet - mantener proporciones en 2x2 */
        @include m.tablet {
            width: 100%;
        }
        /* Desktop - mantener proporciones en fila */
        @include m.desktop {
            width: 100%;
        }
    }
}

.Cif{
    .ValorCif{
        position: absolute;
        font-size: 3.8rem;
        font-family: v.$Texto_titulos_secundarios;
        margin-top: 5rem;
        margin-left: -2rem;   
        opacity: 0;
        @include m.desktop{
            font-size: 2.2vw;
            margin-top: 2.7%;
        }     

    }
    .TexCif{
        position: absolute;
        text-align: center;
        width:35rem;
        margin-top: 22rem;
        font-size: 2rem;
        font-family: v.$Texto_letra;
        /* Estado inicial opcional (si quieres que est oculto antes de la animacin) */
        opacity: 0;

        @include m.tablet{
            margin-top: 28%;
            font-size: 2.2VW;
            width: 45%;
        }
        @include m.desktop{
            width:22%;
            margin-top: 12%;
            font-size: 1vw;
        }
    }
    .especial{
        margin-top: 18rem;
        @include m.tablet{
            margin-top: 25%;
        }
        @include m.desktop{
            margin-top: 10%;
        }
        
    }
}

//Aadimos un keyframe de las cifras


@keyframes animacionTextoNos {
    0% {
        transform: translateY(80px);
        opacity: 0;
    }
    10% {
        transform: translateY(70px);
        opacity: 0.1;
    }
    20% {
        transform: translateY(60px);
        opacity: 0.2;
    }
    30% {
        transform: translateY(50px);
        opacity: 0.3;
    }
    40% {
        transform: translateY(40px);
        opacity: 0.4;
    }
    50% {
        transform: translateY(30px);
        opacity: 0.5;
    }
    60% {
        transform: translateY(20px);
        opacity: 0.6;
    }
    70% {
        transform: translateY(15px);
        opacity: 0.7;
    }
    80% {
        transform: translateY(10px);
        opacity: 0.8;
    }
    90% {
        transform: translateY(5px);
        opacity: 0.9;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes animacionNumerosNos {
    0% {
        transform: scaleY(0);
        transform-origin: bottom center;
        opacity: 0;
    }
    10% {
        transform: scaleY(0.1);
        transform-origin: bottom center;
        opacity: 0.2;
    }
    20% {
        transform: scaleY(0.2);
        transform-origin: bottom center;
        opacity: 0.3;
    }
    30% {
        transform: scaleY(0.3);
        transform-origin: bottom center;
        opacity: 0.4;
    }
    40% {
        transform: scaleY(0.4);
        transform-origin: bottom center;
        opacity: 0.5;
    }
    50% {
        transform: scaleY(0.5);
        transform-origin: bottom center;
        opacity: 0.6;
    }
    60% {
        transform: scaleY(0.6);
        transform-origin: bottom center;
        opacity: 0.7;
    }
    70% {
        transform: scaleY(0.7);
        transform-origin: bottom center;
        opacity: 0.8;
    }
    80% {
        transform: scaleY(0.85);
        transform-origin: bottom center;
        opacity: 0.9;
    }
    90% {
        transform: scaleY(0.95);
        transform-origin: bottom center;
        opacity: 0.95;
    }
    100% {
        transform: scaleY(1);
        transform-origin: bottom center;
        opacity: 1;
    }
}