@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;


.formulario {
    background-color: v.$Cafe;
    display: grid;
    grid-template-columns: 2fr 1fr; 
    grid-template-rows: auto auto; 
    gap: 10px 30px;
    padding: 20px;

    /* Distribucin de elementos */
    .item1 {
        grid-column: 1;
        grid-row: 1;
         
        width: 100%;
        height: 100%;
    }

    .item2 {
        grid-column: 1; /* Primera columna */
        grid-row: 2; /* Segunda fila */
        //quitamos el tipo de cursor
    }
    .sincursor{
        display: block;
        cursor: default;
        @include m.desktop{
            margin-left: 15%;
        }
    }

    .item3 {
        grid-column: 2; /* Primera columna */
        grid-row: 1; /* Tercera fila */
        //Le aadimos un hover para ordenadores unicamente
        a{
            letter-spacing: 0; 
            color: v.$blanco;
            @include m.desktop{
                &:hover{
                    color: v.$amarillo;
                }
            }
        }
    }  
}
/* Estilo adicional */
textarea {
    width: 80%;
    padding: .5%;
    border-radius: 5px;
    height: 4.5vh;
    background-color: v.$ColorFondo;
}

p {
    margin: 10px 0;
    color: v.$ColorFondo;
    font-size: 2rem;
    text-align: left;
    border-bottom: 1rem;

}

.boton{
    border-radius: 5px;
    background-color: v.$blanco;
    margin-left: 70%;
    padding: 1%;
    transition: background-color .5s;
    color: v.$Cafe;
    @include m.desktop{
        padding: .5%;
        background-color: v.$ColorFondo;
        &:hover{
            cursor: pointer;
            background-color: v.$amarillo;
        }
    }
}

