@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

// Carrusel con Scroll Snap mejorado
.carousel {
    width: 100%;
    margin: 0 auto -5%;
    overflow: hidden;
    position: relative;
    
    p {
        font-size: 4vw;
        color: v.$CafeUbicaciones;
        width: 85%;
        text-align: center;
        margin: auto;
    }
    
    .carousel-track-container {
        overflow-x: auto;
        overflow-y: hidden;
        -webkit-overflow-scrolling: touch; // Mejor desplazamiento tctil
        scrollbar-width: none; // Ocultar scrollbar en Firefox
        -ms-overflow-style: none; // Ocultar scrollbar en IE/Edge
        
        &::-webkit-scrollbar {
            display: none; // Ocultar scrollbar en Chrome/Safari
        }
        
        // Scroll Snap mejorado
        scroll-snap-type: x mandatory;
        scroll-behavior: smooth;
    }
  
    .carousel-track {
        display: flex;
        padding: 5%;
        list-style: none;
        margin: 0;
        gap: 5%; // Espacio entre slides
        padding-left: 10%;
        // Agregar espacio al final con pseudo-elemento
        &::after {
            content: '';
            flex: 0 0 5%; // Espacio fijo al final sin afectar otros elementos
        }
    }
  
    .carousel-slide {
        flex: 0 0 85%; // Tamao fijo para todos los elementos
        scroll-snap-align: center;
        scroll-snap-stop: always;
        
        // QUITAR el padding-right del ltimo elemento
        // &:last-child {
        //     padding-right: 5%; // ELIMINAR ESTA LNEA
        // }
        
        a {
            display: block;
            width: 100%;
        }
        
        img,video {
            width: 100%;
            height: auto;
            display: block;
            border-radius: 10px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
        }
    }
    
    // Indicadores del carrusel
    .carousel-indicators {
        display: flex;
        justify-content: center;
        gap: 8px;
        margin-top: 10px;
        padding-bottom: 15px;
        
        .indicator {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #ccc;
            transition: background-color 0.3s ease;
            cursor: pointer;
            
            &.active {
                background-color: v.$CafeUbicaciones;
            }
        }
    }
}

.carousel{
    .prev,.next{
        position: absolute;
        width: 14%;
        border-radius: 50%;
    }
    .prev {
        left: 1.5%;
        top: 50%;
        transform: translateY(-50%) rotateY(180deg);
        z-index: 10;
    }
    .next {
        right: 1.5%;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
    }
}

.carousel-slide{
    position: relative;
    p{
        background-color: v.$verdeOscuroUnete;
        position: absolute;
        text-align: center;
        bottom: 10%;
        font-size: 6.5vw;
        width: 80%;
        font-family: v.$Texto_titulos;
        transform: translateX(12%);
        color: v.$blanco;
    }
}