@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

.UnetePasos{
    h3{
        color: v.$verdeUnete;
        font-size: 8.5vw;
        margin-bottom: 2%;
        @include m.tablet{
            font-size: 6.5vw;
        }
        @include m.desktop{
            font-size: 4vw;
            margin-bottom: 0;
        }
    }
    h2{
        text-align: center;
        color: v.$CafeUbicaciones;
        font-size: 9.5vw;
        margin-bottom: 5%;
        @include m.tablet{
            font-size: 7.5vw;
        }
        @include m.desktop{
            font-size: 5vw;
            margin-bottom: 5%;
        }
    }
    .Bolsa{
        width: 100%;
        @include m.desktop{
            width: 110%;
        }
    }
    .Pasos{
        margin: 5% 0;
        margin-top: 2%;
    }
    @include m.desktop{
       margin-bottom: 0%;
    }
}
.telefonoBolsa{
    
    @include m.desktop{
        display: none;
    }
}
.dekstopBolsa{
    display: none;

    @include m.desktop{
        display: block;
    }
}


//estilo boton general sectioonunete

.SectionUnete{
    .boton_unete{
        width: 50%;
        margin: 0 auto;
        margin-bottom: 5%;
        @include m.tablet{
            width: 30%;
        }
        @include m.desktop{
            width: 40%;
            transition: box-shadow .3s, transform .3s, border-radius .3s;
            &:hover{
                box-shadow: 0px 0px 10px v.$verdeUnete;
                transform: scale(1.05);
                border-radius: 18px;
            }
        }
    }
}

//Inversion de texto e imagen en desktop y mobile
.UnetePasos {
    .ContendorPasos {
        display: flex;
        flex-direction: column;
        @include m.desktop {
            flex-direction: row;
            justify-content: flex-start; 
            position: relative;
        }
    }
    .Texto {
        order: 1;
        z-index: 3;
        @include m.desktop {
            position: absolute;
            order: 2;
            align-self: center;
            right: 2%;
        }
    }
    .Imagen {
        order: 2;
        @include m.desktop {
            width: 58%;
            order: 1;
            position: relative; 
            left: 0%; 
        }
    }
}

//Seccion de procesoflayer solo informacion
.ProcesoFlayer{
    margin: 8% 0;
    h2{
        text-align: center;
        font-size: 7.5vw;
        width: 90%;
        color: v.$CafeUbicaciones;
        font-family: v.$Texto_titulos;
        margin: auto;
        margin-bottom: 5%;
        @include m.tablet{
            font-size: 5.5rem;
            width: 70%;
            margin: auto;
            margin-bottom: 5%;
        }
        @include m.desktop{
            font-size: 5vw;
            width: 60%;
        }
    }
    
}

.Flayerunete{
    width: 100%;
    position: relative;
    overflow: hidden;
    @include m.desktop{
       overflow: visible;
    }
    svg{
        height: 65vh;
        @include m.desktop{
            width: 100%;
            height: auto;
        }
    }
    h3{
        position: absolute;
        text-align: center;
        font-size: 6.5vw;
        color: v.$verdeOscuroUnete;
        bottom: 5%;
        
        @include m.tablet{
            width: 90%;
            left: 50%;
            transform: translateX(-50%);
            font-size: 4rem;
            bottom: 2%;
        }
        @include m.desktop{
            font-size: 4vw;
            width: 50%;
            left: 4%;
            top: 50%;
            transform: translateY(-50%);
        }
        
    }
    .Residuosimg {
        position: absolute;
        z-index: 2;
        width: 40vh;
        height: auto;
        left: 50%;
        transform: translateX(-50%);
        top: 8%; 
        @include m.desktop{
            width: 35%;
            left: 58%;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}

.Preguntas{
    padding: 0 5%;
    margin: 5% auto;
    margin-bottom: 5%;
    overflow: hidden;
    .Preg1{
        margin-bottom: 10%;
        position: relative;
        .PlatanoPreguntas1{
            display: none;
            @include m.desktop{
                
                display: block;
                position: absolute;
                width: 70%;
                height: auto;
                margin: auto;
                top: 15%;
                right: -45%;
            }
        }
        .TextoNoesbasura{
            display: none;

            @include m.desktop{
                display: block;
                position: absolute;
                right: -20%;
                top: 45%;
                width: 20%;
                color: v.$verdeOscuroUnete;
                font-size: 3.5vw;
            }
        }
    }

    .Preg2{
        position: relative;
        .planetapreguntas{
            width: 75%;
            margin: auto;
            margin-top: 10%;
            @include m.tablet{
                width: 50%;
            }

            @include m.desktop{
                position: absolute;
                width: 34%;
                height: auto;
                right: -10%;
                top: 24%;
            }
        }
    }
}

//ajustar el tamao de las preguntas en desktop

.Preg2, .Preg1{
    @include m.desktop{
        width: 72%;
        margin-left: 10%;
    }
}


//Ultima seccion de unete

.SeParteYFamilias{
    h2{
        text-align: center;
        font-size: 5vw;
        color: v.$CafeTextoUneteBoton;
        margin-bottom: 2%;
        @include m.tablet{
            font-size: 4.5vw;
        }
        @include m.desktop{
            font-size: 3.5vw;
        }
    }
    .boton2{
        width: 35%;
        @include m.tablet{
            width: 25%;
        }
        @include m.desktop{
            display: none;
        }
    }
}

.Familias{
    margin-bottom: -4%;
    position: relative;
    z-index: -1;
    h3,h5{
        color: v.$verdeOscuroUnete;
        text-align: center;
        margin: auto;
        position: absolute;
        font-family: v.$Texto_titulos_secundarios;
    }

    h3{
        bottom: 40%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 9.5vw;
    }
    h5 {
        position: absolute;
        bottom: 28%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 4vw;
    }
    img{
        width: 100%;
        height: auto;
    }
}

@keyframes Aparecer {
    0% {
        opacity: 0;
        transform: translate(-50%, 50%);
    }
    100% {
        opacity: 1;
        transform: translate(-50%, 0);
    }
}
