@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

//Todo el estilo para la navegacion desktop
.NavegacionDesktop{
    display: none;
    @include m.desktop{
        display: block;
        z-index: 5;
    }
    
}
.Redes_princ {
    @include m.desktop{
        background-color: v.$Cafe;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }
    h1 {
        color: v.$ColorFondo;
        margin: 0;
        font-size: 1.8vw;
        letter-spacing: 1px;
    }

    a,
    p,
    img {
        color: v.$ColorFondo;
    }
}

h1.TextoIniciar {
    font-size: 2vw;
    transition: color .3s linear;
    letter-spacing: 1px;
    //El "&" sirve para juntar propiedades por ejemplo .TextoIniciar:hover
    //Si quitamos el "&" tenemos .TextoIniciar :hover (Con espacio) y eso no existe
    //lo hacemos que se muestre unicamente en desktops
    @include m.desktop {
        &:hover {
            color: v.$amarillo;
        }
    }

}

.InicioS svg,
.InicioS a {
    vertical-align: middle;
    transition: margin-left 1s;
}

.InicioS,
.Redes {
    width: 20%;
    border: none;
    display: flex;
    justify-content: space-around;
}

.InicioS svg {
    vertical-align: middle;
    position: absolute;
    width: 1.5vw;
    padding: 0;
    margin: 0;
    left: 17%;
    transition: width .5s,color .5s,left 1s;
}

.BusquedaInS{
    right: 70%;
    position: absolute;
    opacity: 0;
    transition: opacity 1s ease-out ;
    input{
        background-color: v.$Cafe;
        font-size: 1.3vw;
        color: v.$Cafe;
        &::placeholder{
            color: v.$Cafe;
            font-size: 1.3vw;
        }
    }
    
