//Parte de busqueda de cubetas por ID

.Redes {
    justify-content: space-around;

    a {
        border: none;

        img {
            padding: 0;
            margin: 0;
            width: 2.7vw;
            transition: filter .2s;
            @include m.desktop {
                &:hover {
                    filter: drop-shadow(0 0 1px v.$amarilloLugares); 
                }
        }
    }
}
}

nav.fixedNav {
    @include m.desktop {
        box-shadow: 0px 0px 5px v.$Verde;
        position: fixed;
        top: 0;
        z-index: 3;
    }

}

.NavPrincipal {
    position: relative;
    @include m.desktop{
        transition: all .3s ease;
        background-color: v.$ColorFondo;
        width: 100%;
        margin: 0;
        display: grid;
        grid-template-columns: 1fr 2.5fr;
    }
    .Logo{
        position: relative;
        display: flex;
        padding: 0;
        height: 5vw;
        width: 60%;
        margin: 0;
        margin-left: 35%; 
        svg{
            position: absolute;
            width: 15%;
            left: -15%;
            transform: translateY(42%);
        }
    }
}
ul.Menu {
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    a {
        position: relative; // Aadir esta lnea
        margin: 0 2rem;
        font-family: v.$Texto_titulos;
        font-size: 1.8vw;
        text-decoration: none; // Aadir esta lnea para eliminar subrayado
        color: inherit; // Aadir esta lnea para heredar el color del texto
        &:last-child {
            &::after {
                display: none; // No mostrar la lnea despus del ltimo enlace
            }
        }
        // aadir el hover para desktop
        @include m.desktop {
            transition: text-shadow .3s;
            // Este hover se ve mejor sin tiempo de transicin
            &:hover {
                text-shadow: 0px 0px 5px v.$amarillo;
            }
        }
        &.active {
            background-color: v.$amarillo_sombra;
            padding: .2% 1.2%;
        }
        &::after {
            //Linea de separacin entre los enlaces
            content: '';
            position: absolute;
            top: 50%;
            right: -2rem; // Ajustar segn sea necesario
            transform: translateY(-50%);
            height: 100%; // Ajustar segn sea necesario
            border-right: 2px solid v.$Cafe; // Cambia el color de la lnea
        }
    }
}

//Todo el estilo para la navegacion mobile

.NavMobile {
    display: flex;
    justify-content: flex-start; /* Alinea hacia la izquierda */
    align-items: center; /* Centra verticalmente */
    padding: .5%;
    svg {
        height: 5vw;
        width: auto;
        margin: 0;
        padding: 0;
        margin-left: 2%;
        cursor: pointer;
    }
    img{
        width: 40%;
        height: 10vw;
    }
    @include m.desktop {
        display: none;
    }
}

//Clase a agregar cuando se despleigue el menu
.BordeCircular{
    border-radius: 0px 0px 5rem 5rem;
}

.NavMobile{
    .IconoIniciarS {
        width: 13%;
        margin-top: 2%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        margin-right: 2%; 
        @include m.tablet{
            width: 10%;
            margin-right: 5%;
            margin-top: 1%;
        }
        @include m.desktop{
            display: none;
        }
        svg {
            width: 100%;
            height: auto;
        }
        }
}