@use 'Base/Variables' as v;
@use 'Base/mixins' as m;


.tabla-resultados-busqueda {
    // Contenedor principal para la tabla y su ttulo
    margin: 2rem;
    color: black;
    padding: 1rem 8rem;

    &__titulo {
        color: v.$VerdeTitUb;
        margin-bottom: 1.5rem;
        font-family: v.$Texto_titulos;
        font-size: 3rem;
        text-align: center;
    }

    &__contenedor {
        overflow-x: auto;
        border-radius: 1rem;
        background-color: white;
        margin-bottom: 2rem;
    }

    &__tabla {
        width: 100%;
        border-collapse: collapse;

        th,
        td {
            padding: 1.2rem 1.5rem;
            text-align: left;
            border-bottom: 0.1rem solid #ddd;
        }
        .CentradoTexto{
            text-align: center;
        }
        th {
            background-color: v.$Cafe;
            color: white;
            font-family: v.$Texto_letra;
            font-size: 1.5rem;
        }

        td {
            font-size: 1.5rem;
            background-color: transparent;
        }

        tr {
            transition: background-color .15s;
            @include m.desktop{
                &:hover {
                    cursor: pointer;
                }
            }

            // Estilos especficos para filas del centro actual
            &.fila-centro-actual {
                @include m.desktop{
                    &:hover {
                    background-color: v.$VerdeSombra_ResultadosBusqueda;
                }
                }
            }

            // Estilos especficos para filas de otros centros
            &.fila-otros-centros {
                @include m.desktop{
                    &:hover {
                        background-color: v.$AzulSombra_ResultadosBusqueda;
                    }
                }
            }
        }
    }

    &__boton {
        background-color: v.$Cafe;
        color: white;
        border: none;
        padding: 0.6rem 1.2rem;
        border-radius: 0.4rem;
        cursor: pointer;
        transition: background-color 0.3s;
        font-family: v.$Texto_titulos_secundarios;
        font-size: 1.4rem;

        &:hover {
            background-color: v.$VerdeTitUb;
        }

        // Variante para usuarios del centro actual
        &--centro {
            background-color: v.$VerdeUb;
            
            &:hover {
                background-color: v.$Verde;
            }
        }
    }
}

// Nuevos estilos para las secciones de usuarios
.seccion-usuarios {
    margin-bottom: 3rem;

    &__titulo {
        color: v.$VerdeUb;
        font-family: v.$Texto_titulos;
        font-size: 2.5rem;
        margin-bottom: 1rem;
        padding: 1rem 1.5rem;
        background-color: rgba(76, 175, 80, 0.1);
        border-left: 0.4rem solid v.$VerdeUb;
        border-radius: 0.5rem;
        display: flex;
        align-items: center;
        gap: 1rem;
        font-weight: 500;
        i {
            font-size: 2rem;
        }

    }

    &__contador {
        font-size: 1.6rem;
        color: #666;
        font-weight: normal;
        margin-left: auto;
    }

    // Modificador para la seccin de otros usuarios
    &--otros {
        .seccion-usuarios__titulo {
            color: v.$azul;
            background-color: rgba(139, 116, 85, 0.1);
            border-left-color: v.$azul;
        }
    }
}


.centro-badge {
    background-color: v.$verdeSombra;
    padding: 0.3rem 0.8rem;
    border-radius: 1rem;
    font-size: 1.8rem;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

// Media queries para responsividad
@media (max-width: 768px) {
    .tabla-resultados-busqueda {
        padding: 0;
        margin: 1rem;

        &__titulo {
            font-size: 2rem;
        }

        &__tabla {
            font-size: 1.2rem;

            th,
            td {
                padding: 0.8rem;
            }
        }

        &__boton {
            padding: 0.4rem 0.8rem;
            font-size: 1.2rem;
        }
    }

    .seccion-usuarios {
        &__titulo {
            font-size: 1.8rem;
            padding: 0.8rem 1rem;
            flex-direction: column;
            text-align: center;
            gap: 0.5rem;
            font-family: v.$Texto_letra;
            i {
                font-size: 1.6rem;
            }
        }

        &__contador {
            font-size: 1.4rem;
            margin-left: 0;
        }
    }

    .cubetas-badge {
        padding: 0.2rem 0.6rem;
        font-size: 1.5rem;
    }


    /* // Ocultar columna de centro en mviles para ahorrar espacio
    .tabla-resultados-busqueda__tabla {
        td:nth-child(4),
        th:nth-child(4) {
            display: none;
        }
    } */
}

/* Estilos para mensajes de insignias */
.mensaje-insignia-asignada {
    background-color: #fcf8e3;
    border: 1px solid #faebcc;
    color: #8a6d3b;
    padding: 10px 15px;
    margin: 10px 0;
    border-radius: 4px;
    animation: fadeIn 0.5s;
    font-weight: bold;
    font-size: 3vw;
    text-align: center;
    @include m.tablet{
        font-size: 2vw;
    }
    @include m.desktop{
        font-size: 1.5vw;
    }
}
.mensajeEstatusDeBolsaAtualizado{
    color: #8a6d3b;
    padding: 10px 15px;
    margin: 10px 0;
    animation: fadeIn 0.5s;
    font-weight: bold;
    font-size: 5vw;
    text-align: center;
    border-bottom: 2px solid v.$verdeSombra;
    width: fit-content;
    align-self: center;
    @include m.tablet{
        font-size: 3vw;
    }
    @include m.desktop{
        font-size: 2.5vw;
    }
}

.ActualizacionDatosNoConcluida{
    font-size: 3vw;
    color: v.$naranjaClarito;
    margin: 2% 0;
    @include m.tablet{
        margin: 1%;
        font-size: 2vw;
    }
    @include m.desktop{
        font-size: 1.5vw;
    }
    li.textocentrado{
        text-align: center;
    }
}


.mensaje_Cubetas_Agregadas, h3.mensajeDeReestablecimientodeDatos,
    h3.mensaje_Cubetas_Modificadas,
    h4.mensaje-insignia-eliminada,
    h2.mensaje-insignia-asignada{
    background-color: v.$Verde;
    color: v.$blanco;
    font-family: v.$Texto_letra;
    width: 100%;
    margin: 2% auto;
    border-radius: 4px;
    padding: 2%;
    animation: fadeIn 0.5s;
    font-weight: 200;
    font-size: 3.5vw;
    text-align: center;
    @include m.tablet{
        padding: 1% 2%;
        max-width: fit-content;
        font-size: 1.3em;
    }
    .color_azul{
        background-color: v.$blanco;
        width: fit-content;
        margin: 0 auto;
        padding: 0% 2%;
        border-radius: .5em;
        margin-top: 1%;
        color: v.$CafeClaroTexto;
        letter-spacing: 2px;
        text-align: center;
        font-family: v.$Texto_letra;
        font-weight: 600;
        font-size: 4vw;
        @include m.tablet{
            font-size: 1.4em;
        }
    }
}

h4.mensaje-insignia-eliminada{
    color: v.$naranjaClarito;
    background-color: v.$blanco;
    font-weight: bold;
    @include m.tablet{
        padding: 1% 2%;
    }
}
h2.mensaje-insignia-asignada{
    background-color: v.$Naranja;
    @include m.tablet{
        padding: 1% 2%;
    }
}


@keyframes fadeIn {
    from { opacity: 0; transform: translateY(-10px); }
    to { opacity: 1; transform: translateY(0); }
}

.ErrorActualizacionCubetas{
    font-size: 3rem;
    color: v.$CafeClaro;
    margin: 1% 0;
}

.busqueda-usuarios {
    text-align: center;
    margin: 0;
    padding: 2rem;
    background-image: url('/vectores/Admin/Busqueda/BLOQUE AMARILLO.svg');



    .encabezado-busqueda {
        margin-bottom: 2rem;

        h1 {
            color: v.$Cafe;
            font-size: 2.5em;
            margin-bottom: 0.5rem;
            font-weight: 100;
            font-family: v.$Texto_titulos;
        }

        h2 {
            color: v.$Cafe;
            font-size: 2.5rem;
            font-family: v.$Texto_titulos;
        }
    }

    .buscador-global {
        background-color: white;
        padding: 2rem;
        border-radius: 1rem;
        margin: 0 auto 3rem auto;
        box-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.1);
        max-width: 80rem;

        form {
            width: 100%;
        }

        .campo-busqueda {
            display: flex;
            gap: 1rem;
            margin-bottom: 1.5rem;

            input {
                flex: 1;
                padding: 1.2rem 1.5rem;
                border: 0.1rem solid #ddd;
                border-radius: 0.5rem;
                font-size: 1.6rem;


                &:focus {
                    outline: none;
                    border-color: v.$Cafe;

                }
            }

            .btn-buscar {
                letter-spacing: 0.5px;
                background-color: v.$Cafe;
                color: white;
                border: none;
                border-radius: 0.5rem;
                padding: 0 2rem;
                cursor: pointer;
                font-size: 1.8rem;
                font-family: v.$Texto_titulos_secundarios;
                transition: background-color 0.3s;

                @include m.desktop{
                    &:hover {
                        background-color: v.$VerdeUb;
                    }
                }
            }
        }

        .opciones-busqueda {
            display: flex;
            justify-content: space-around;
            .link-opcion {
                padding: 1.5% 2%;
                border-radius: 20px;
                background-color: v.$verdeSombra;
                margin: 2% 0;
                margin-bottom: 5%;
                color: v.$Cafe;
                text-decoration: none;
                font-weight:300;
                font-family: v.$Texto_titulos;
                font-size: 2.5rem;
                transition: color 0.2s;
                @include m.tablet{
                    font-size: 3rem;
                }
                @include m.desktop{
                    &:hover {
                        color: v.$VerdeUb;
                    }
                }
            }
        }
    }

    .mensaje {
        background-color: white;
        border-left: 0.4rem solid v.$VerdeUb;
        padding: 1.5rem;
        margin: 0 auto 2rem auto;
        text-align: left;
        max-width: 80rem;
        font-size: 1.6rem;
        color: #333;
    }
}

// Media queries para responsividad
@media (max-width: 768px) {
    .busqueda-usuarios {
        padding: 1rem;

        .encabezado-busqueda {
            h1 {
                font-size: 2em;
            }

            h2 {
                font-size: 2rem;
            }
        }

        .buscador-global {
            padding: 1.5rem;
            

            .campo-busqueda {
                flex-direction: column;

                input {
                    padding: 1rem;
                }

                .btn-buscar {
                    padding: 1rem;
                    width: 100%;
                }
            }

            .opciones-busqueda {
                flex-direction: column;
                gap: 1rem;
            }
        }
    }
}



// Estilos para la seccin de detalle de usuario y aumento de cubetas
.detalle-usuario {

    background-color: transparent;
    border-radius: 1rem;
    margin: 2rem 0rem;



    &__header {
        text-align: center;
        margin-bottom: 2rem;
        padding-bottom: 1rem;
        border-bottom: 0.1rem solid #eee;
    }

    &__titulo {
        color: v.$Cafe;
        font-family: v.$Texto_letra;
        font-size: 2.8rem;
        text-align: center;
        margin-bottom: 0.5rem;
    }

    &__nombre {
        font-size: 2.2rem;
        color: v.$Cafe;
        font-family: v.$Texto_titulos_secundarios;
        text-align: center;
    }

    &__mensaje {
        background-color: #f1f8e9;
        color: v.$VerdeUb;
        padding: 1rem;
        border-radius: 0.5rem;
        margin-bottom: 1.5rem;
        text-align: center;
        font-weight: bold;
        font-size: 1.6rem;
    }

    // Estructura de dos columnas
    &__contenido {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        height: auto;
        background-image: url('/vectores/Admin/Busqueda/BLOQUE AMARILLO.svg');
        background-size: cover;
        margin-bottom: 3rem;
        padding-left: 4rem;
    }

    &__columna {
        flex: 1;
        min-width: 30rem;

        // Modificador para la columna de la imagen
        &--imagen {
            display: flex;
            align-items: center;
            justify-content: center;
            @include m.tablet{
                flex-direction: column;
            }
        }
    }

    &__imagen-contenedor {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 5%;
        @include m.tablet{
            margin: 0;
            padding: 0;
            width: 60%;
            height: auto;
            margin: 5% 0;
        }
        @include m.desktop{
            width: 40%;
        }
    }

    &__imagen {
        max-width: 100%;
        max-height: 30rem;
        object-fit: contain;
        @include m.tablet{
            width: 100%;
            height: 100%;
        }
    }

    &__cubetas {
        padding: 1rem;
    }

    &__subtitulo {
        color: v.$ColorFondo;
        background-color: v.$Cafe;
        font-family: v.$Texto_titulos;
        font-size: 3rem;
        font-weight: bold;
        margin: 2rem;
        margin-bottom: 1.5rem;
        text-align: center;
        padding: 1rem;
        border-radius: 1rem;
    }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 1rem;
    }

    &__campo-grupo {
        display: flex;
        gap: 1rem;
        align-items: center;
        margin-bottom: 1.5rem;
    }

    &__input-wrapper {
        position: relative;
    }

    &__input {
        width: 10rem;
        padding: 1rem;
        font-size: 1.8rem;
        border: 0.1rem solid #ddd;
        border-radius: 0.5rem;
        text-align: center;

        &:focus {
            outline: none;
            border-color: v.$VerdeUb;
        }
    }

    &__btn {
        background-color: v.$Rojo ;
        color: white;
        border: none;
        border-radius: 0.5rem;
        padding: 1rem 2rem;
        font-size: 1.6rem;
        font-family: v.$Texto_letra;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
            background-color: v.$Cafe;
        }
    }

    &__progreso-contenedor {
        margin: 1rem 0;
        background-color: v.$blanco;
        padding: 1rem;
        border-radius: 1.5rem;
    }

    &__progreso-texto {
        text-align: center;
        margin-bottom: 1rem;
        font-size: 2rem;
        color: v.$Cafe;
        font-weight: bold;
    }

    &__barra-progreso {
        height: 2rem;
        background-color: #f0f0f0;
        border-radius: 1rem;
        overflow: hidden;
        margin-bottom: 1.5rem;
    }

    &__progreso {
        height: 100%;
        background-color: v.$Verde;
        transition: width 0.5s ease;
    }

    &__meta-alcanzada {
        font-family: v.$Texto_letra;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
        padding: 1rem;
        background-color:  v.$ColorFondo;
        border-radius: 1rem;
        animation: pulso 2s infinite alternate;
    }

    &__composta-icon {
        font-size: 3rem;
        margin-bottom: 1rem;
    }

    &__composta-texto {
        color: v.$VerdeUb;
        font-family: v.$Texto_titulos;
        font-size: 2.2rem;
        font-weight: bold;
    }

    &__totales {
        text-align: center;
        margin-top: 2rem;
        font-size: 1.6rem;

        strong {
            color: v.$negro;
            font-size: 1.8rem;
        }
    }

    p {
        color: v.$Cafe;
        margin-left: 2rem;
    }
}

// Animacin de pulso para el mensaje de meta alcanzada
@keyframes pulso {
    0% {
        transform: scale(1);
        box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.6);
    }

    70% {
        transform: scale(1.05);
        box-shadow: 0 0 0 10px rgba(76, 175, 80, 0);
    }

    100% {
        transform: scale(1);
        box-shadow: 0 0 0 0 rgba(76, 175, 80, 0);
    }
}

// Media queries para responsividad
@media (max-width: 768px) {
    .detalle-usuario {
        padding: 1.5rem;

        &__titulo {
            font-size: 2.2rem;
        }

        &__nombre {
            font-size: 1.8rem;
        }

        &__subtitulo {
            font-size: 1.8rem;
        }

        &__columna {
            min-width: 100%;
        }

        &__campo-grupo {
            flex-direction: column;
            gap: 1rem;
        }

        &__input {
            width: 8rem;
        }

        &__btn {
            width: 100%;
        }

        &__composta-texto {
            font-size: 1.8rem;
        }

        &__imagen {
            max-height: 20rem;
        }
    }
}


//cambio de rol 
.detalle-usuario__cambiar-rol {
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap; // Cambio de nowrap a wrap para permitir flexibilidad
    border-radius: 1rem;

    width: 80%;
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.1); // Aadir sombra sutil
    margin: auto;
    justify-content: center; // Distribuir elementos uniformemente
    @include m.tablet{
        width: auto;
        padding: 2rem;
        margin: 2rem 10rem;

    }
}

h4.detalle-usuario__subtitulo {
    background-color: transparent;
    color: v.$blanco;
    margin-right: 1rem;
    white-space: nowrap;
    font-size: 1.5em;
    font-weight: 100;
    letter-spacing: 1px;
    background-color: v.$Cafe;
    flex: 0 0 auto; // No crecer ni se encoger, mantendr su tamao
}

.detalle-usuario__form-rol {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-grow: 1;
    justify-content: center; 
    width: auto;
    flex-wrap: wrap; // Permitir flexibilidad en dispositivos pequeos
}


.detalle-usuario__rol-actual {
    margin-right: 1.5rem;
    flex: 0 0 auto; // No crecer ni se encoger
}

.detalle-usuario__rol-actual p {
    color: black;
    margin: 0;
    white-space: nowrap;
}

.detalle-usuario__rol-label {
    color: black;
    
    white-space: nowrap;
    flex: 0 0 auto; // No crecer ni se encoger
}

.detalle-usuario__botones-rol {
    display: flex;
    border-radius: 0.5rem;
    padding: 1rem;

    justify-content: center; // Centrar botones
}

.MensajesErrorEditUserBusqueda{
    h3{
        font-size: 1.5em;
        margin-bottom: 2%;
        @include m.tablet{
            font-size: 2em;
        }
