        margin: 5% 0;
        text-align: center;
        list-style: none;
        
        @include m.tablet{
            margin: 2% 0;
        }
        li{
            margin: 4% auto;
            border-bottom: 2px solid v.$verdeSombra;
            width: fit-content;
            @include m.tablet{
                margin: 2% auto;
                font-size: 1.2em;
            }
            @include m.desktop{
                margin: 1% auto;
            }
