.detalle-usuario__btn-rol {
    margin: 0 2rem;
    padding: 1rem 2rem; // Mayor padding horizontal
    border: none;
    border-radius: 0.5rem; // Ms redondeado
    cursor: pointer;
    background-color: v.$Cafe;
    color: v.$ColorFondo;
    font-weight: bold;
    transition: all 0.3s ease; // Transicin suave
    
    &:hover {
        transform: translateY(-2px); // Efecto de elevacin al pasar el mouse
        box-shadow: 0 0.3rem 0.6rem rgba(0, 0, 0, 0.2);
    }
}

.detalle-usuario__btn-rol.activo {
    background-color: v.$amarillo;
    color: white;
    box-shadow: 0 0.2rem 0.4rem rgba(0, 0, 0, 0.2);
}

// Media query para dispositivos mviles
@media (max-width: 768px) {
    .detalle-usuario__cambiar-rol {
        flex-direction: column;
        align-items: stretch;
        padding: 1.5rem;
        margin-bottom: 8rem !important; // Mantener espacio inferior en mviles
    }
    
    .detalle-usuario__subtitulo {
        margin-right: 0;
        margin-bottom: 1rem;
        text-align: center;
    }
    
    .detalle-usuario__form-rol {
        flex-direction: column;
        align-items: center;
    }
    
    .detalle-usuario__rol-actual,
    .detalle-usuario__rol-label {
        margin: 0.5rem 0;
        text-align: center;
    }
    
    .detalle-usuario__botones-rol {
        margin: 1rem auto;
        width: 100%;
    }
    
    .detalle-usuario__btn-rol {
        flex: 1;
        margin: 0 0.5rem;
        padding: 0.8rem 1.2rem;
    }
}


// Estilos para los mensajes de confirmacin de cambio de rol
.mensaje-rol-actualizado {
    background-color: #d4edda;
    border-left: 0.5rem solid v.$VerdeUb;
    color: v.$VerdeTitUb;
    padding: 1.5rem;
    margin: 1.5rem auto;
    border-radius: 0.5rem;
    text-align: center;
    font-family: v.$Texto_titulos;
    font-weight: bold;
    animation: fadeInMessage 0.5s ease-in-out;
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.1);
    
    strong {
        color: v.$VerdeTitUb;
