.tabla_AprobacionBolsasComposta {
    padding: 3%;

    .TablaAdminBolsas {
        width: 100%;
        border-collapse: collapse;
        font-family: v.$Texto_letra;

        table {
            width: 100%;
        }

        th, td {
            border: 1px solid v.$Cafe;
            padding: 3% 4%;
            text-align: center;
            font-size: 4vw;

            @include m.tablet {
                font-size: 1.8vw;
                padding: 1.5% 3%;
            }

            @include m.desktop {
                font-size: 1.4vw;
            }
        }

        th {
            background-color: v.$amarilloTit;
            color: v.$CafeUbicaciones;
        }

        input[type="checkbox"] {
            transform: scale(1.5);
            cursor: pointer;

            @include m.desktop {
                transform: scale(1.2);
            }
        }

        .BtnEnviarEstatus {
            padding: 1% 2%;
            background-color: v.$verdeUnete;
            color: v.$ColorFondo;
            font-size: 2rem;
            border-radius: 10px;
            font-family: v.$Texto_titulos_secundarios;
            cursor: pointer;
            display: block;
            margin: 5% auto 0 auto;

            @include m.tablet {
                font-size: 1.7vw;
                margin-top: 1%;                
            }

            @include m.desktop {
                margin-top: 2%;
                font-size: 1.2vw;
                transition: box-shadow .2s;
                &:hover {
                    box-shadow: 0 0 10px 2px v.$Verde;
                }
            }
        }
    }
    @include m.tablet{
        margin-top: 5%;
        padding: 0;
        width: 80%;
    }
    @include m.desktop{
        width: 60%;
    }
}

.mensaje-rol-actualizado,
.mensaje-rol-sin-cambios,
.mensaje-rol-error{
    font-size: 4vw;
    @include m.tablet{
        font-size: 2vw;
    }
} 