@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

.ProcesoComposta{
     h3{
        font-size: 7.5vw;
        margin: 0;
        padding: 2%;
        background-color: v.$amarilloTit;
        color: v.$CafeUbicaciones;
        margin-bottom: 2%;
        @include m.tablet{
            padding: .5%;
            font-size: 4.2vw;
            letter-spacing: 0px;
        }
    }
    p{
        font-size: 3vw;
        font-weight: 550;
        text-align: center;
        color: v.$CafeClaroTexto;
        margin: 0 auto;
        width: 98%;
        margin-bottom: -5%;
        @include m.desktop{
            font-size: 2.5vw;
        }
    }

}

.FotosSectionComp{
    margin-top: -10%;
    z-index: 2;
    position: relative;
    .carousel{
        overflow:visible;
        position: absolute;
        top: 21%;
        left: 19%;
        width: 55%;
        margin: auto;
        .carousel-slide{
            flex: 0 0 100%;
        }
        .carousel-indicators{
            position: absolute;
            bottom: -2rem;
            right: 40%;
            .indicator{
                width: 1.8vw;
                height: 1.8vw;
            }
        }
        @include m.desktop{
            display: none;
        }
    }
    .Fondo {
        @include m.desktop {
            width: 100%;
            height: 62vw;
            margin: auto;
        }
    }
}

.imagenesFotosComp{
    position: absolute;
    display: flex;
    flex-direction: row;
    gap: 1.5%;
    display: none;
    justify-content: center;
    @include m.desktop{
        display: flex;
        top: 32%;
    }
    img{
        width: 28%;
    }
}

.ComoUtilizarComp{
    position: relative;
    display: block;
    overflow: hidden;
    margin-top: -15%;
    .FondoTierraComp{
        position: relative;
        h2{
            position: absolute;
            top: 25%;
            left: 7%;
            font-size: 6.5vw;
            width: 45%;
            color: v.$CafeUbicaciones;
            @include m.desktop{
                width: 35%;
                font-size: 4.5vw;
                left: 15%;
                top: 32%;
            }
        }
        .Bolsa{
            position: absolute;
            top: 12%;
            right: -5%;
            width: 50%;
            height: auto;
        }
        .Flecha{
            position: absolute;
            bottom: 2%;
            width: 45%;
            height: auto;
            right: 19%;
            @include m.desktop{
                width: 60%;
                right: 14%;
                bottom: -8%;
            }
        }
        a{
            font-size: 3.5vw;
            bottom: 35%;
            left: 11%;
            border-radius: 5px;
            font-family: v.$Texto_titulos;
            padding: 1% 2%;
            position: absolute;
            color: v.$Cafe;
            background-color: v.$amarillo;

            @include m.desktop{
                bottom: 36%;
                font-size: 2.5vw;
                left: 17%;
                transition: transform .3s, box-shadow .3s;
                border-radius: 10px;
                &:hover{
                    transform: scale(1.02);
                    box-shadow: 0px 0px 10px 2px v.$verdeOscuroUnete;
                }
            }
        }
    }
}