@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

.informacion-usuario {
    margin: 2rem auto;
    max-width: 1200px;
    position: relative;
    padding-bottom: 0;
    margin-bottom: 0;
    
    // Nuevo header para ttulo y botn
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 1.5rem;
        padding: 0 1rem;
        
        @include m.tablet {
            padding: 0 2rem;
        }
        
        @include m.desktop {
            padding: 0 3rem;
        }
    }
    
    &__titulo {
        font-weight: 100;
        color: v.$VerdeUb;
        margin: 0;
        flex: 1;
        text-align: center;
        
        @include m.tablet {
            text-align: left;
            font-size: 3rem;
        }
        
        @include m.desktop {
            font-size: 3.2rem;
        }
    }
    
    &__tabla {
        table {
            width: 100%;
            border-collapse: separate;
            border-spacing: 10px;
            height: 100%;
            margin-bottom: 0;
        }
        
        td {
            padding: 0;
            vertical-align: middle;
            height: 100%;
            width: 33.333%;
            
            &[rowspan="2"] {
                vertical-align: middle;
                height: 100%;
            }
        }
        
        // Estilos responsivos
        @media (max-width: 768px) {
            table,
            tbody,
            tr,
            td {
                display: block;
                width: 100%;
            }
            
            td {
                margin-bottom: 10px;
                
                &[rowspan] {
                    height: auto;
                }
            }
        }
    }
    
    // Estilos para el contenedor de la imagen
    &__imagen-completa {
        z-index: -1;
        position: relative;
        margin-top: -28vw;
        width: 100%;
    }
}

// Botn de editar perfil
.boton-editar-perfil {
    display: flex;
    align-items: center;
    background: linear-gradient(135deg, v.$Verde);
    color: white;
    padding: 0.8rem 1.5rem;
    border-radius: 25px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    margin-right: 3%;
    
    &__icono {
        width: 20px;
        height: 20px;
        filter: brightness(0) invert(1);
        
        @include m.tablet {
            width: 24px;
            height: 24px;
        }
    }
    
    &__texto {
        font-size: 1.4rem;
        
        @include m.tablet {
            font-size: 1.6rem;
        }
        
        @include m.desktop {
            font-size: 1.8rem;
        }
    }
    
    // Responsive: solo icono en mvil
    @media (max-width: 768px) {
        padding: 0.8rem;
        
        &__texto {
            display: none;
        }
        
        &__icono {
            width: 5vw;
            height: auto;
        }
    }
    @include m.tablet{
        margin: 0;
        margin-left: 1%;
        padding: .5rem 1rem;
        border-radius: 10px;
    }
    @include m.desktop{
        &:hover {
            transform: translateY(-2px);
            box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
            background: linear-gradient(135deg);
        }
    }
}

// Modal para editar perfil
.modal-editar {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(5px);
    align-items: center;
    justify-content: center;
    padding: 2rem;
    opacity: 0;
    transition: opacity 0.3s ease;
    
    // Estado activo: visible y opaco
    &.modal-activo {
        display: flex !important;
        opacity: 1;
        
        .modal-editar__contenido {
            transform: scale(1) translateY(0);
        }
    }
    
    // Estado cerrado: oculto
    &.modal-cerrado {
        display: none !important;
        opacity: 0;
    }
    
    &__contenido {
        background: white;
        border-radius: 15px;
        width: 100%;
        max-width: 900px;
        max-height: 90vh;
        overflow-y: auto;
        box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
        transform: scale(0.8) translateY(-50px);
        transition: transform 0.3s ease;
        
        @include m.tablet {
            max-width: 1000px;
            padding: 0;
        }
        
        @include m.desktop {
            max-width: 1200px;
        }
    }
    
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        border-bottom: 2px solid #eee;
        background: linear-gradient(135deg, v.$Verde);
        color: white;
        border-radius: 15px 15px 0 0;
        
        @include m.tablet {
            padding: 2.5rem 3rem;
        }
    }
    
    &__titulo {
        margin: 0;
        font-size: 2.2rem;
        
        @include m.tablet {
            font-size: 2.6rem;
        }
        
        @include m.desktop {
            font-size: 3rem;
        }
    }
    
    &__cerrar {
        background: none;
        border: none;
        font-size: 3rem;
        color: white;
        cursor: pointer;
        padding: 0;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.3s ease;
        
        &:hover {
            background: rgba(255, 255, 255, 0.2);
            transform: rotate(90deg);
        }
    }
    
    &__formulario {
        padding: 2rem;
        
        @include m.tablet {
            padding: 3rem;
        }
    }
    
    &__grid {
        display: grid;
        gap: 3rem;
        
        @include m.tablet {
            grid-template-columns: 1fr 1fr;
            gap: 4rem;
        }
    }
    
    &__columna {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    
    &__subtitulo {
        color: v.$VerdeUb;
        font-size: 2rem;
        margin: 0 0 1rem 0;
        padding-bottom: 0.5rem;
        border-bottom: 2px solid v.$Verde;
        
        @include m.tablet {
            font-size: 2.2rem;
        }
        
        @include m.desktop {
            font-size: 2.4rem;
        }
    }
    
    &__campo {
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        
        label {
            font-weight: bold;
            color: #333;
            font-size: 1.4rem;
            
            @include m.tablet {
                font-size: 1.6rem;
            }
        }
        
        input,
        select {
            padding: 1.2rem;
            border: 2px solid #ddd;
            border-radius: 8px;
            font-size: 1.4rem;
            transition: all 0.3s ease;
            background: white;
            
            &:focus {
                outline: none;
                border-color: v.$Verde;
                box-shadow: 0 0 0 3px rgba(v.$Verde, 0.1);
            }
            
            @include m.tablet {
                padding: 1.4rem;
                font-size: 1.6rem;
            }
        }
        
        // Campo especial para reestablecer contrasea
        &--especial {
            background: #f8f9fa;
            padding: 2rem;
            border-radius: 10px;
            border: 2px dashed #dee2e6;
            text-align: center;
            margin-top: 1rem;
        }
    }
    
    &__ayuda {
        font-size: 1.2rem;
        color: #666;
        margin: 0.5rem 0 0 0;
        font-style: italic;
        
        @include m.tablet {
            font-size: 1.3rem;
        }
    }
    
    &__botones {
        display: flex;
        gap: 1.5rem;
        justify-content: center;
        margin-top: 3rem;
        padding-top: 2rem;
        border-top: 2px solid #eee;
        
        @include m.tablet {
            justify-content: flex-end;
            gap: 2rem;
        }
    }
}

// Botones del modal
.boton-modal {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    padding: 1.2rem 2.4rem;
    border: none;
    border-radius: 8px;
    font-size: 1.4rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    min-width: 140px;
    justify-content: center;
    
    @include m.tablet {
        padding: 1.4rem 2.8rem;
        font-size: 1.6rem;
        min-width: 160px;
    }
    
    i {
        font-size: 1.2rem;
        
        @include m.tablet {
            font-size: 1.4rem;
        }
    }
    
    &--cancelar {
        background: #6c757d;
        color: white;
        
        &:hover {
            background: #5a6268;
            transform: translateY(-2px);
            box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
        }
    }
    
    &--guardar {
        background: linear-gradient(135deg, v.$Verde);
        color: white;
        
        &:hover {
            background: linear-gradient(135deg);
            transform: translateY(-2px);
            box-shadow: 0 4px 15px rgba(v.$Verde, 0.3);
        }
    }
}

// Botn de reestablecer contrasea
.boton-reestablecer {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    background: linear-gradient(135deg, #ffc107);
    color: #333;
    border: none;
    padding: 1rem 2rem;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
    font-size: 1.4rem;
    
    &:hover {
        background: linear-gradient(135deg);
        transform: translateY(-2px);
        box-shadow: 0 4px 15px rgba(#ffc107, 0.3);
    }
    
    i {
        font-size: 1.2rem;
    }
    
    @include m.tablet {
        padding: 1.2rem 2.4rem;
        font-size: 1.6rem;
    }
}

// Estilos originales de las tarjetas
.tarjeta {
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    height: 100%;
    background-color: v.$Verde; // Verde por defecto
    display: flex;
    flex-direction: column;
    
    // Variantes de altura
    &--alta {
        height: 100%;
    }
    
    // Variante dividida
    &--dividida {
        display: flex;
        flex-direction: row;
        border-radius: 10px;
        overflow: visible; // Cambiado de hidden a visible
        gap: 5px;
        height: auto;
        background-color: transparent; // Fondo transparente para el contenedor
        box-shadow: none; // Eliminar sombra del contenedor
        
        @media (max-width: 768px) {
            flex-direction: column;
        }
    }
    
    // Tarjeta especfica para Centro de Acopio
    &--centro-acopio {
        background-color: v.$amarillo; // Amarillo para Centro de Acopio
        
        .tarjeta__encabezado,
        .tarjeta__contenido {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 2rem; // Padding uniforme
            font-size: 3rem;
            font-weight: 200;
        }
        
        .tarjeta__contenido {
            flex: 1; // Permitir que crezca para ocupar espacio
            color: black;
            font-size: 3.5rem;
            padding-top: 0;
            font-weight: bold;
        }
    }
    
    // Elementos internos
    &__mitad {
        flex: 1;
        border-radius: 10px;
        overflow: hidden;
        background-color: v.$Verde;
        display: flex;
        flex-direction: column;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); // Agregar sombra a cada mitad
        
        @media (max-width: 768px) {
            margin-bottom: 5px;
        }
    }
    
    &__encabezado {
        color: black;
        padding: 10px;
        text-align: center;
        font-weight: bold;
        margin: 0; // Eliminar mrgenes
    }
    
    &__contenido {
        padding: 10px 15px;
        text-align: center;
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 30px;
        color: white; // TEXTO BLANCO para todos los resultados
        margin: 0; // Eliminar mrgenes
        padding-top: 0;
    }
}

// Animacin para el modal
@keyframes modalAppear {
    from {
        opacity: 0;
        transform: scale(0.8) translateY(-50px);
    }
    to {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}

// Clase para imagen de ancho completo
.imagen-ancho-completo {
    width: 100%;
    height: auto;
    display: block;
}

// Estilos adicionales para mejorar la experiencia en mvil
@media (max-width: 480px) {
    .modal-editar {
        padding: 1rem;
        
        &__contenido {
            border-radius: 10px;
        }
        
        &__header {
            padding: 1.5rem;
            border-radius: 10px 10px 0 0;
        }
        
        &__titulo {
            font-size: 1.8rem;
        }
        
        &__formulario {
            padding: 1.5rem;
        }
        
        &__grid {
            gap: 2rem;
        }
        
        &__botones {
            flex-direction: column;
            gap: 1rem;
        }
    }
    
    .boton-modal {
        width: 100%;
        min-width: auto;
    }
    
    .boton-reestablecer {
        width: 100%;
        justify-content: center;
    }
}

.Direccion_target{
    border: 2px solid black;
    max-width: 50%;
}