@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

.administrar-centros {
    text-align: center;
    margin: 0rem 2rem;
    padding-top: 1rem;

    .fila-titulo {
        color: white;
        background-color: v.$Verde;
        padding: 2rem;
        font-size: 5rem;
        font-family: v.$Texto_titulos;
        border-radius: 0rem 0rem 1em 1em;

    }

    .fila-subtitulo {
        color: v.$VerdeUb;
        padding: 3.5rem;
        padding-top: 4rem;
        font-size: 4rem;
        font-family: v.$Texto_titulos;
        font-weight: bold;

    }

    .fila-estadisticas {
        background-color: v.$amarillo;
        width: 16rem;
        align-items: center;
        padding: 0.5rem;
        font-size: 2.5rem;
        border-radius: 1rem;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 2rem;
        font-family: v.$Texto_titulos;
        &:hover {
            background-color: v.$Verde;
        }
        
    }

   
    .contenedor-boton-estadisticas {
        background-color: v.$amarillo;
        width: 16rem;
        align-items: center;
        padding: 0.5rem;
        font-size: 2.5rem;
        border-radius: 1rem;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 2rem;        
    }

    .boton-estadisticas {
        display: inline-block;
        color: black;
        text-decoration: none;
        font-weight: bold;
        transition: all 0.3s;
        width: auto;
        letter-spacing: 0.5px;
        font-family: v.$Texto_letra;
    }

    .fila-centros {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        padding: 20px;

        @media (min-width: 1200px) {
            grid-template-columns: repeat(6, 1fr);
        }
    }

    .centro-acopio {
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all 0.3s;

        &:hover .centro-contenido {
            transform: translateY(-5px);
            box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
        }

        &.seleccionado .centro-contenido {
            border: 3px solid v.$VerdeUb;
            background-color: #F1F8E9;
        }
    }

    .centro-contenido {
        background-color: white;
        border-radius: 10px;
        padding: 15px;
        text-align: center;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        transition: all 0.3s;
        width: 100%;
        height: 25rem;
        margin-bottom: 10px;
    }

    .centro-nombre {
        font-size: 3rem;
        font-weight: bold;
        color: v.$VerdeUb;
        margin-bottom: 10px;
        font-family: v.$Texto_titulos;
    }

    .centro-icono {
        margin: 15px 0;
        height: 150px;
        display: flex;
        align-items: center;
        justify-content: center;

        svg,
        img {
            width: auto;
            height: auto;
            max-height: 140px;
        }
    }

    .centro-botones {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;
    }

    .boton-registro,
    .boton-busqueda {
        display: block;
        padding: 8px 0;
        text-align: center;
        text-decoration: none;
        border-radius: 5px;
        transition: all 0.3s;
        font-size: 1.2em;
        font-family: v.$Texto_titulos;
        &:hover {
            background-color: v.$Verde;
        }
    }

    .boton-busqueda{
        &:hover {
            background-color: v.$VerdeUb;
        }
    }

    .boton-registro {
        background-color: v.$amarillo;
        color: black;
        letter-spacing: 0.5px;
    }

    .boton-busqueda {
        background-color: v.$Verde;
        color: white;
        letter-spacing: 0.5px;
    }

    img {
        width: 8rem;
    }
}

// Estilos para la seccin de usuarios
.seccion-usuarios-centro {
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 20px;

    h2 {
        color: v.$VerdeUb;
        border-bottom: 2px solid v.$VerdeUb;
        padding-bottom: 10px;
        margin-bottom: 20px;
        font-family: v.$Texto_titulos;
        font-size: 2.5rem;
    }

    .buscador-centro {
        margin-bottom: 20px;

        form {
            display: flex;
            gap: 10px;
        }

        input {
            flex: 1;
            padding: 10px;
            border: 1px solid #ddd;
            border-radius: 5px;
            font-size: 16px;
        }

        button {
            background-color: v.$VerdeUb;
            color: white;
            border: none;
            border-radius: 5px;
            padding: 10px 15px;
            cursor: pointer;
            font-family: v.$Texto_titulos;
        }
    }

    .tabla-usuarios {
        width: 100%;
        border-collapse: collapse;
        margin-top: 15px;

        th,
        td {
            padding: 12px;
            text-align: left;
            border-bottom: 1px solid #ddd;
        }

        th {
            background-color: v.$Verde;
            color: white;
            font-family: v.$Texto_titulos;
        }

        tr:hover {
            background-color: #f5f5f5;
        }
    }

    .btn-ver {
        background-color: v.$VerdeUb;
        color: white;
        border: none;
        padding: 6px 12px;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;
        font-family: v.$Texto_titulos;

        &:hover {
            background-color: v.$VerdeUb;
        }
    }

    .mensaje {
        padding: 15px;
        background-color: #F1F8E9;
        border-left: 5px solid v.$VerdeUb;
        margin: 10px 0;
        color: #333;
        font-family: v.$Texto_titulos;
    }
}

// Media queries para responsividad
@media (max-width: 1199px) {
    .administrar-centros {
        .fila-centros {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

@media (max-width: 992px) {
    .administrar-centros {
        .fila-centros {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media (max-width: 768px) {
    .administrar-centros {
        .fila-centros {
            grid-template-columns: 1fr;
        }
    }

    .seccion-usuarios-centro {
        .buscador-centro {
            form {
                flex-direction: column;
            }
        }
    }
}