@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

.MiCubeta{
     h3{
        font-size: 7.5vw;
        margin: 0;
        padding: 2%;
        background-color: v.$amarilloTit;
        color: v.$CafeUbicaciones;
        margin-bottom: -8%;
        @include m.tablet{
            padding: .5%;
            font-size: 4.2vw;
            letter-spacing: 0px;
        }
    }
    p{
        font-size: 3.7vw;
        font-weight: 550;
        text-align: center;
        color: v.$CafeClaroTexto;
        margin: 0 auto;
        margin-bottom: -5%;
        width: 98%;
        @include m.tablet{
            font-size: 1.3em;
        }  
        @include m.desktop{
            font-size: 2em;
        }
    }
    .PlacaVerd{
        margin-top: -15%;        

    }
    .ListadoDeCosasSi{
        margin-top: -7%;
    }
    .muneca{
        margin-top: -15%;
    }
}