/* Versin bsica del spinner - 100% opaco */
.loader {
    border: 5px solid v.$CafeClaro;
    border-radius: 50%;
    border-top: 5px solid v.$Cafe;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
    // No se necesita opacidad aqu, ya es completamente visible
}

.loader-text {
    margin-top: 15px;
    font-family: Arial, sans-serif;
    color: v.$Cafe;
    font-size: 16px;
    letter-spacing: 1px;
    // No se necesita opacidad aqu, ya es completamente visible
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}