@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

.administrador {
    background-color: v.$Cafe;
    padding: 2%;
    margin: 5% auto;
    margin-top: 2%;
    border-radius: 30px;
    width: 90%;

    h1 {
        color: v.$amarilloLugares;
        text-align: center;
        font-size: 2.5vw;
        margin-bottom: 30px;
    }

    h2 {
        color: v.$amarilloLugares;
        font-size: 1.8vw;
        margin: 2% 0;
    }

    h3 {
        color: v.$amarilloLugares;
        font-size: 1.5vw;
        margin: 1.5% 0;
    }

    // Estilos para los formularios generales
    form {
        background-color: #2a1810;
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 40px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 20px;
        align-items: center;

        &.form-inline {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            padding: 15px;
            
            .campo-inline {
                display: flex;
                align-items: center;
                gap: 10px;
                margin-right: 15px;
                
                label {
                    margin-bottom: 0;
                    white-space: nowrap;
                }
                
                input, select {
                    width: auto;
                    min-width: 80px;
                }
            }
            
            button {
                margin-left: auto;
            }
        }

        label {
            color: v.$amarilloLugares;
            font-size: 1.3vw;
            font-weight: bold;
            display: block;
            margin-bottom: 10px;
        }

        input, select {
            width: 100%;
            padding: 12px;
            border: 3px solid v.$amarilloLugares;
            border-radius: 15px;
            background-color: #4c2b1d;
            color: white;
            font-size: 1.2vw;

            &:focus {
                outline: none;
                border-color: #ffdb99;
                box-shadow: 0 0 8px rgba(v.$amarilloLugares, 0.4);
            }
            
            &::placeholder {
                color: rgba(255, 255, 255, 0.5);
            }
        }

        button, a {
            background-color: v.$amarilloLugares;
            color: v.$Cafe;
            padding: 15px 30px;
            border: none;
            border-radius: 15px;
            font-size: 1.3vw;
            font-weight: bold;
            cursor: pointer;
            transition: all 0.3s ease;
            justify-self: center;
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 8px;

            .fa, .fas, .far {
                margin-right: 5px;
            }

            &:hover {
                background-color: v.$amarilloLugares;
                transform: scale(1.05);
            }
            
            &.btn-accion {
                padding: 10px 20px;
                font-size: 1.1vw;
            }
            
            &.btn-seleccionar {
                padding: 8px 15px;
                font-size: 1vw;
                margin: 0;
                border-radius: 10px;
            }
            
            &.btn-buscar {
                padding: 12px 25px;
                font-size: 1.2vw;
                width: 200px;
                margin: 0 auto;
                justify-content: center;
            }
        }
    }

    // Estilos especficos para el formulario de bsqueda
    .form-busqueda {
        display: flex;
        flex-direction: column;
        padding: 20px;
        align-items: center;
        
        .campos-busqueda {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
            gap: 20px;
            width: 100%;
            margin-bottom: 20px;
            
            .campo {
                display: flex;
                flex-direction: column;
            }
        }
    }

    // Estilos para mensajes
    .mensaje {
        background-color: v.$amarilloLugares;
        color: v.$Cafe;
        padding: 15px;
        border-radius: 10px;
        text-align: center;
        font-weight: bold;
        font-size: 1.2vw;
        margin: 20px 0;
        line-height: 1.5;
        word-wrap: break-word;
        
        &.error {
            background-color: #ff6b6b;
            color: white;
        }
        
        &.destacado {
            background-color: #4CAF50;
            color: white;
            font-size: 1.3vw;
            padding: 20px;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
            animation: destacado-pulso 2s infinite;
        }
    }
    
    @keyframes destacado-pulso {
        0% { box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.7); }
        70% { box-shadow: 0 0 0 15px rgba(76, 175, 80, 0); }
        100% { box-shadow: 0 0 0 0 rgba(76, 175, 80, 0); }
    }
    
    @keyframes pulso {
        0% { transform: scale(1); }
        50% { transform: scale(1.05); }
        100% { transform: scale(1); }
    }

    .perfil-usuario {
        background-color: #2a1810;
        padding: 30px;
        border-radius: 20px;
        margin-top: 30px;

        h3 {
            color: v.$amarilloLugares;
            font-size: 1.6vw;
            margin-bottom: 20px;
            text-align: center;
            border-bottom: 2px solid rgba(v.$amarilloLugares, 0.3);
            padding-bottom: 10px;
        }

        .perfil {
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
            gap: 15px;
            width: auto;
            height: auto;
            
            .dato {
                color: white;
                font-size: 1.2vw;
                padding: 15px;
                background-color: rgba(255, 255, 255, 0.05);
                border-radius: 10px;
                display: flex;
                flex-direction: column;
                word-break: break-word;
                overflow: hidden;
                
                &[data-label]::before {
                    content: attr(data-label);
                    font-weight: bold;
                    color: v.$amarilloLugares;
                    margin-bottom: 8px;
                    font-size: 1vw;
                }
                
                span {
                    font-size: 1.3vw;
                    word-wrap: break-word;
                    overflow-wrap: break-word;
                    max-width: 100%;
                }
            }
        }
    }
    
    .panel-usuario {
        background-color: #2a1810;
        border-radius: 20px;
        padding: 25px;
        margin-top: 30px;
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
        animation: fade-in 0.5s ease-out;
        
        @keyframes fade-in {
            from { opacity: 0; transform: translateY(20px); }
            to { opacity: 1; transform: translateY(0); }
        }
        
        .acciones-rapidas {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
            gap: 20px;
            margin-bottom: 20px;
            
            .cubetas, .cambiar-rol {
                background-color: #3a2110;
                border-radius: 15px;
                padding: 20px;
                
                h3 {
                    margin-top: 0;
                    padding-bottom: 10px;
                    border-bottom: 1px solid rgba(v.$amarilloLugares, 0.3);
                    margin-bottom: 15px;
                }
            }
            
            .alerta-premio {
                background-color: rgba(76, 175, 80, 0.2);
                color: #4CAF50;
                padding: 15px;
                border-radius: 10px;
                margin-top: 15px;
                text-align: center;
                font-weight: bold;
                font-size: 1.2vw;
                border: 2px dashed #4CAF50;
                transition: all 0.3s ease;
                
                &.pulso {
                    background-color: rgba(76, 175, 80, 0.3);
                    transform: scale(1.03);
                }
                
                .fa-star {
                    color: v.$amarilloLugares;
                }
            }
            
            .info-premio {
                margin-top: 15px;
                
                p {
                    color: white;
                    margin-bottom: 8px;
                    text-align: center;
                }
                
                .progress-bar {
                    height: 12px;
                    background-color: rgba(255, 255, 255, 0.1);
                    border-radius: 6px;
                    overflow: hidden;
                    
                    .progress {
                        height: 100%;
                        background-color: v.$amarilloLugares;
                        transition: width 0.5s ease;
                    }
                }
            }
        }
    }

    .resultados-busqueda {
        margin: 30px 0;
        
        h3 {
            margin-bottom: 15px;
        }
        
        .tabla-resultados {
            background-color: #2a1810;
            border-radius: 15px;
            overflow: hidden;
            padding: 5px;
            
            table {
                width: 100%;
                border-collapse: collapse;
                
                th {
                    background-color: #4c2b1d;
                    color: v.$amarilloLugares;
                    padding: 15px;
                    text-align: left;
                    font-size: 1.1vw;
                }
                
                td {
                    padding: 12px 15px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
                    color: white;
                    font-size: 1.1vw;
                }
                
                tr {
                    cursor: pointer;
                    transition: background-color 0.2s;
                    
                    &:hover {
                        background-color: #3a2110;
                    }
                    
                    &:last-child td {
                        border-bottom: none;
                    }
                }
                
                .acciones {
                    text-align: right;
                    white-space: nowrap;
                }
            }
        }
    }

    .buscador {
        margin-bottom: 20px;
        
        h2 {
            margin-bottom: 15px;
            text-align: center;
        }
    }

    .menu-admin {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
    }

    .boton-registro {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 15px 25px;
        background-color: v.$amarilloLugares;
        color: v.$Cafe;
        text-decoration: none;
        border-radius: 15px;
        font-size: 1.3vw;
        font-weight: bold;
        transition: all 0.3s ease;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
        width: fit-content;
        border: none;
        cursor: pointer;

        .icono-registro {
            margin-right: 10px;
            font-size: 1.5vw;
        }

        &:hover {
            background-color: v.$amarilloLugares;
            transform: scale(1.05);
            box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
        }

        &:active {
            transform: scale(0.98);
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
        }
    }
    
    // Estilos para la seccin de registro
    .seccion-registro {
        margin: 30px 0;
        animation: fadeIn 0.5s ease-out;
        
        @keyframes fadeIn {
            from {
                opacity: 0;
                transform: translateY(20px);
            }
            to {
                opacity: 1;
                transform: translateY(0);
            }
        }
        
        h2 {
            text-align: center;
            margin-bottom: 20px;
        }
        
        .form-registro {
            padding: 30px;
            
            .campos-registro {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
                gap: 20px;
                margin-bottom: 20px;
            }
            
            .info-password {
                grid-column: 1 / -1;
                background-color: rgba(255, 255, 255, 0.05);
                padding: 15px;
                border-radius: 10px;
                border-left: 4px solid v.$amarilloLugares;
                
                .nota {
                    color: v.$amarilloLugares;
                    margin: 0;
                    font-size: 1.1vw;
                }
            }
            
            .botones-registro {
                display: flex;
                justify-content: center;
                gap: 20px;
                margin-top: 20px;
                
                button {
                    width: auto;
                    min-width: 200px;
                }
                
                .btn-cancelar {
                    background-color: #ff6b6b;
                    color: white;
                    
                    &:hover {
                        background-color:#ff6b6b;
                    }
                }
            }
        }
    }

    @media (max-width: 768px) {
        padding: 20px;
        margin: 20px;
        width: auto;

        h1 {
            font-size: 24px;
        }

        h2 {
            font-size: 20px;
        }
        
        h3 {
            font-size: 18px;
        }

        form {
            grid-template-columns: 1fr;
            padding: 20px;
            
            &.form-busqueda {
                .campos-busqueda {
                    grid-template-columns: 1fr;
                }
                
                button {
                    width: 100%;
                }
            }
            
            &.form-inline {
                flex-direction: column;
                
                .campo-inline {
                    width: 100%;
                    margin-bottom: 15px;
                }
                
                button {
                    width: 100%;
                    margin-left: 0;
                }
            }

            label, input, select, button {
                font-size: 16px;
            }
        }
        
        .boton-registro {
            font-size: 16px;
            padding: 12px 20px;
            
            .icono-registro {
                font-size: 18px;
            }
        }
        
        .panel-usuario {
            .acciones-rapidas {
                grid-template-columns: 1fr;
            }
        }
        
        .resultados-busqueda {
            .tabla-resultados {
                overflow-x: auto;
                
                table {
                    th, td {
                        font-size: 14px;
                        padding: 10px;
                    }
                }
            }
        }

        .perfil-usuario {
            .perfil {
                grid-template-columns: 1fr;
                
                .dato {
                    font-size: 16px;
                    
                    &[data-label]::before {
                        font-size: 14px;
                    }
                    
                    span {
                        font-size: 16px;
                    }
                }
            }
        }
        
        .mensaje {
            font-size: 16px;
        }
        
        .seccion-registro {
            .form-registro {
                .campos-registro {
                    grid-template-columns: 1fr;
                }
                
                .info-password {
                    .nota {
                        font-size: 14px;
                    }
                }
                
                .botones-registro {
                    flex-direction: column;
                    
                    button {
                        width: 100%;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}