@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

.Ubicaciones{
    margin-bottom: 5%;
    h3{
        font-size: 7.5vw;
        margin: 0;
        padding: 2%;
        background-color: v.$amarilloTit;
        color: v.$CafeUbicaciones;
        margin-bottom: 2%;
        @include m.tablet{
            padding: .5%;
            font-size: 4.2vw;
        }
    }
    p{
        font-size: 3.5vw;
        font-weight: 550;
        text-align: center;
        color: v.$CafeClaroTexto;
        margin: 0 auto;
        width:80%;
        @include m.tablet{
            font-size: 2.5vw;
        }
        @include m.desktop{
            width: 80%;
            font-size: 2.2vw;
        }
    }
    .carousel-slide {
        flex: 0 0 110%; 
    }
}

.locations-grid {
    overflow: hidden;
    margin: 0 2%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    .location-item{
        margin: 5% 0;
        position: relative;
        margin-bottom: 10%;
        //border-bottom: 2px solid black;
        &:nth-child(2){
            img{
                position: absolute;
                top: 4.6%;
            }
        }
        &:nth-child(3){
            img{
                position: absolute;
                bottom: -4.5%;
            }
        }
        &:nth-child(4){
            img{
                margin-left: 50%;
            }
        }
        &:nth-child(5){
            img{
                position: absolute;
                top: -2%;
                left: 50%;
            }
        }
    }   
    margin-bottom: -5%;
}

//Agregamos el estilo para cada link
.locations-grid{
    div{
        a{
            position: absolute;
            color: v.$blanco;
            text-decoration: none;
            font-size: 2vw;
            font-family: v.$Texto_titulos;
            display: block;
            padding: .5% 0;
            background-color: v.$VerdeUb;
            text-align: center;
            width: 33%;
            margin: 0 auto;
            border-radius: 10px;
            transition: transform .5s;
            @include m.desktop{
                &:hover{
                transform: scale(1.15);
            }
            }
            
        }
    }
    div:nth-child(1){
        a{
            bottom:4%;
            left: 33%;
        }
    }
    div:nth-child(2){
        a{
            bottom: 4.8%;
            left: 35.4%;
        }
    }
    div:nth-child(3){
        a{
            bottom: 4%;
            left: 36%;
        }
    }
    div:nth-child(4){
        a{
            z-index: 5;
            bottom: 7.2%;
            left: 85%;
        }
    }
    div:nth-child(5){
        a{
            z-index: 2;
            bottom: 6.5%;
            left: 88%;
        }
    }
}
// Responsive helpers 
.mobile-only {
    display: block;
    @include m.tablet {
      display: none;
    }
  }
  
.desktop-only {
    display: none;
    @include m.tablet {
        display: grid;
    }
}

//Modificacion de Carrusel de ubicaciones
.Ubicaciones{
    .carousel-slide {
        flex: 0 0 85%; // Tamao fijo para todos los elementos
        scroll-snap-align: center;
        scroll-snap-stop: always;
        
        // QUITAR el padding-right del ltimo elemento
        // &:last-child {
        //     padding-right: 5%; // ELIMINAR ESTA LNEA
        // }
        //Hacer que se centre el primer elemento
        &:first-child{
            margin-left: 15%;
        }
        
        a {
            display: block;
            width: 100%;
        }
        
        img {
            width: 100%;
            height: auto;
            display: block;
            box-shadow: none;
        }
    }
    .carousel-track {
        display: flex;
        padding: 5%;
        list-style: none;
        margin: auto;
        gap: 5%; // Espacio entre slides
        // Agregar espacio al final con pseudo-elemento
    }
    
    // Estilos adicionales para el carrusel infinito
    .carousel {
        margin-bottom: 5%;
        .carousel-track {
            // Ajustar padding para acomodar los clones
            padding-left: 5%; // Reducir para que el clone sea visible
            transition: none; /* Se maneja dinmicamente */
            // Eliminar el pseudo-elemento ::after ya que ahora tenemos clones
            &::after {
                display: none;
            }
        }

    }
}