@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

.NavMobile{
    .toggleMenu{
    height: auto;
    width: 8%;
    @include m.tablet{
        width: 5%;
        }
    }
    img{
        height: auto;
        width: 45%;
        @include m.tablet{
            width: 30%;
        }
    }

    .menuPrincipalMobile{
        display: none;
        background-color: v.$CafeClaro;
        width: 60%;
        height: 58rem;
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 0px 0px 5rem 0px;
        box-shadow: 0px 0px 10px v.$Verde;
        z-index: 8;
        padding-top: 7vw;
        @include m.tablet{
            padding-top: 4rem;
            width: 40%;
        }
        h3{
            font-size: 6rem;
            margin-bottom: 3.5rem;
        }
        p {
            position: absolute;
            top: -1.5%;
            padding: 4vw 5.5vw;
            background-color: v.$amarillo;
            right: 0;
            border-radius: 0px 0px 0px 15px;
            cursor: pointer;
            &::after {
                content: "-";
                display: block;
                right: 12%;
                position: absolute;
                top: -14vw;
                font-size: 20vw;
                color: red;
                pointer-events: none;
            }
            @include m.tablet{
                padding: 3vw 4vw;
                &::after {
                    font-size: 15vw;
                    top: -10.5vw;
                }
            }
        }
        li{
            list-style: none;
            display: flex;
            flex-direction: column;
            align-items: center; 
            a{
                text-align: center;
                margin-bottom: 2.7rem;
                font-family: v.$Texto_titulos;
                width: 70%;
                border: 2px solid v.$gris_sombra;
                font-size: 1.2em;
                border-radius: 2rem;
                padding-top: .5rem;
                box-shadow: -2px 2px 1px v.$blanco;
            }
        }
    }
}