@use '../Base/Variables.scss' as v;
@use '../Base/mixins.scss' as m;

.Contacto{
     h3{
        font-size: 7.5vw;
        margin: 0;
        padding: 2%;
        background-color: v.$amarilloTit;
        color: v.$CafeUbicaciones;
        margin-bottom: 2%;
        @include m.tablet{
            padding: .5%;
            font-size: 4.2vw;
            letter-spacing: 0px;
        }
    }
    p{
        font-size: 3vw;
        font-weight: 550;
        text-align: center;
        color: v.$CafeClaroTexto;
        margin: 0 auto;
        width: 98%;
        @include m.desktop{
            font-size: 2.2vw;
        }
    }
    span{
        display: block;
    }
}

.NumeroTextoYrecuadroContacto{
    position: relative;
    //border: 2px solid black;
    overflow: hidden;
    .Numero {
        display: inline-flex; //debe ser flex para que aplique el float
        position: relative;
        padding: 0;
        margin: 6% 12% 8% 0; // margen de 5% a la derecha
        float: right; // lo alinea a la derecha
        @include m.desktop{
            //border: 2px solid black;
            margin: 6% 0 5% 15%;
            float: left;
        }
        img {
            position: absolute;
            left: -34%;
            bottom: 52%;
            transform: translateY(50%);
            width: 35%;
            height: auto;
            pointer-events: none;
        }

        h4 {
            text-align: center;
            font-family: v.$Texto_letra_italica;
            font-size: 5.5vw;
            @include m.desktop{
                text-align: left;
                font-size: 3vw;
                //border: 2px solid black;
                transition: text-shadow .3s;
                &:hover{
                    text-shadow: 0px 0px 5px v.$Verde;
                }
            }
        }
    }
    p{
        position: relative;
        clear: right; 
        @include m.desktop{
            clear: left;
            text-align: left;
            margin-left: 5%;
        }
    }
    .recuadro{
        p{
            font-size: 4.5vw;
            width: 45%;
            margin-left: 8%;
            @include m.tablet{
                width: 50%;
            }
            @include m.desktop{
                display: block;
                font-size: 3vw;
                text-align: center;
                margin-left: 6%;
            }
        }
        form{
            textarea{
                margin: 2% 0;
                border: 3px solid  v.$Cafe;
                border-radius: 0px 6rem 0 0;
                display: block;
                width: 70%;
                height: 10rem;
                margin-left: 2%;
                padding: 1%;
                padding-right: 4%;
                text-align: left; 
                @include m.desktop{
                    height: 17rem;
                    width: 50%;
                    margin-left: 5%;
                    margin-top: 1%;
                }
            }
            button{
                display: block;
            }
        }
    }
    .manoConPhone{
        position: absolute;
        width: 35%;
        height: auto;
        bottom: -2%;
        right: -2%;
        @include m.desktop{
            bottom: -5%;
            right: 0%;
        }
    }
}

.recuadro{
    margin-bottom: 5%;
    .BtnEnviar{
        cursor: pointer;
        padding: 0% 4%;
        border-radius: 20px;
        margin-left: 42%;
        background-color: v.$verdeUnete;
        @include m.desktop{
            padding: 0 2%;
            transition: box-shadow .2s;
            &:hover{
                box-shadow: 0px 0px 10px 2px v.$Verde;
            }
        }
        p{
        color: v.$ColorFondo;
        font-family: v.$Texto_titulos;
        font-size: 4vw;
        letter-spacing: .5px;
        margin-left: -.5%;
        @include m.tablet{
            font-size: 2vw;
        }   
        }
    }   
}

.FigurasDeRedes{
    margin-top: -1%;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 2%;
    .divdeAbsolute{
        width: 100%;
        position: absolute;
    }
    .RedesHorizontal{
        display: block;
        display: flex;
        flex-direction: row;
        gap: 5%;
        align-items: center;
        justify-content: center;
        margin-top: 1.5%;
        a{
            width: 12%;
            border-radius: 50%;
            transition: filter .3s, background-color .3s;
            @include m.tablet{
                width: 7%;
                
            }
            @include m.desktop{
                &:hover{
                    filter: drop-shadow( 0px 0px 5px v.$amarilloTit);
                    background-color: v.$amarillo;
                }
            }
        }
        @include m.tablet{
            margin-top: 1%;
        }
        }
    }
div.TexodeContacto{
    //border: 2px solid v.$verdeUnete;
    margin-top: 2%;
    h2,h4{
        color: v.$CafeUbicaciones;
        text-align: center;
    }
    h2{
        font-size: 5vw;
        margin-bottom: 1%;
        @include m.tablet{
            margin-bottom: 2%;
        }
    }
    h4{
        font-size: 3vw;
        font-family: v.$Texto_letra;
        font-weight: 200;
        @include m.tablet{
            font-size: 2vw;
        }
    }
}