@use 'Variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
    background-color: v.$ColorFondo;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    border: none; 
    outline: none; 
}
.mobile-only {
  display: block;
  @include m.tablet {
    display: none;
  }
}

.desktop-only {
  display: none;
  @include m.tablet {
    display: block;
  }
}
body {
    font-family: v.$Texto_letra;
    font-optical-sizing: auto;
    font-style: normal;
    color: v.$Cafe;
    font-size: 2rem;

    &.overflow-hidden {
        overflow: hidden;
    }
}
a,h1,h2,h3,h4{
    @include m.desktop{
        letter-spacing: 0px;
    }
}
p {
    font-size: 2rem;
    color: v.$Cafe;
    line-height: 1.5;
    font-family: v.$Texto_letra;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
    color: v.$Cafe;
    font-family: v.$Texto_letra_italica;

    border: none; 
}
h1, h2, h3 {
    margin: 0 0 5rem 0;
    font-weight: 900;
    color: v.$Cafe;
    font-family: v.$Texto_titulos;

    border: none; 
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 4.6rem;
}
h3 {
    font-size: 6rem;
    text-align: center;
}
img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    display: block;
    border: none; 
}
body > section {
    padding: 10rem 0;
}
.justificado{
    text-align: justify;
}
.centrado{
    text-align: center;
}
.notMargin{
    margin: 0;
}
.notPadding{
    padding: 0;
}

svg {
    color: white;
    border: none; 
    outline: none;
    margin: 5px auto 0 auto;
}

html, body {
    height: 100%; /* Asegura que el contenedor ocupe toda la altura */
    margin: 0;
  }
  
  body {
    display: flex;
    flex-direction: column; /* Disea los elementos en una columna */
    min-height: 100vh; /* Altura mnima para cubrir la pantalla completa */
  }
  
  footer {
    margin-top: auto; /* Empuja el footer al final */
    width: 100%; /* Asegura que ocupe todo el ancho */
  }


