@use 'Base/Variables' as v;
@use 'Base/mixins' as m;

// Contenedor principal con todos los estilos encapsulados
.estadisticas-contenedor {
    width: 100%;
    margin: 2rem 0;
    padding: 0 4%;
    border-radius: 1.5rem;

    //Boton de regresar
    .BtnRegresarEstadisticas{
        text-align: center;
        margin: auto;
        width: 100%;
        background:none;
        padding: 1%;
        margin-top: 2%;
        a{
            font-family: v.$Texto_letra;
            padding: 1% 5%;
            border-radius: 15px;
            font-size: 2rem;
            font-weight: 600;
            background-color: v.$verdeSombra;
            @include m.tablet{
                font-size: 2.5rem;
            }
            @include m.desktop{
                padding: .5% 2%;
                font-size: 3rem;
                transition: color .2s;
                &:hover{
                    color: v.$VerdeUb;
                }
            }
        }
        @include m.desktop{
            margin-top: 1%;
        }
    }
    // Ttulo especfico para estadsticas
    .estadisticas-titulo {
        color: v.$VerdeUb;
        text-align: center;
        margin: 3rem 0;
        padding-bottom: 1.5rem;
        font-size: 3.2rem;
        font-weight: 700;
        position: relative;
        letter-spacing: 0.05em;
        
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 12rem;
            height: 0.6rem;
            background: linear-gradient(90deg, v.$Verde, v.$VerdeUb);
            border-radius: 2rem;
            box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.1);
        }
    }

    // Panel de Estadsticas Globales mejorado
    .panel-estadisticas-globales {
        margin-bottom: 4rem;
        padding: 2rem;
        border-radius: 1.5rem;
        background: linear-gradient(145deg, rgba(255,255,255,0.7), rgba(255,255,255,0.9));
        .estadisticas-seccion-titulo {
            color: v.$VerdeUb;
            margin-bottom: 2.5rem;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
            position: relative;
            padding-bottom: 1rem;
            
            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 8rem;
                height: 0.3rem;
                background: v.$Verde;
                border-radius: 1rem;
            }
        }

        .tarjetas-estadisticas {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            gap: 2.5rem;
            
            .tarjeta-estadistica {
                flex: 1;
                min-width: 22rem;
                display: flex;
                align-items: center;
                padding: 2.5rem;
                border-radius: 1.2rem;
                background: white;
                border: none;
                box-shadow: 0px 0px 2px v.$verdelogin;
                transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                
                &:hover {
                    transform: translateY(-0.8rem);
                    box-shadow: 0 1rem 2.5rem rgba(0, 0, 0, 0.1), 
                                inset 0 0.5rem 0.5rem rgba(255, 255, 255, 0.8);
                }
                
                .icono {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 7rem;
                    height: 7rem;
                    border-radius: 50%;
                    background: linear-gradient(135deg, rgba(v.$Verde, 0.1), rgba(v.$VerdeUb, 0.3));
                    margin-right: 2.5rem;
                    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1),
                                inset 0 0.5rem 1rem rgba(255, 255, 255, 0.5);
                    
                    i {
                        font-size: 2.8rem;
                        color: v.$VerdeUb;
                        text-shadow: 0 0.2rem 0.2rem rgba(255, 255, 255, 0.5);
                    }
                }
                
                .detalles {
                    .estadistica-subtitulo {
                        color: v.$VerdeUb;
                        font-size: 1.6rem;
                        margin-bottom: 0.8rem;
                        font-weight: 500;
                        letter-spacing: 0.05em;
                    }
                    
                    .numero {
                        font-size: 3.5rem;
                        font-weight: 700;
                        color: v.$Verde;
                        line-height: 1.2;
                        letter-spacing: 0.02em;
                        text-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.1);
                    }
                }
                
                // Estilos especficos para diferenciar tarjetas
                &:nth-child(1) {
                    .icono {
                        background: linear-gradient(135deg, rgba(76, 175, 80, 0.1), rgba(76, 175, 80, 0.3));
                        i { color: #4CAF50; }
                    }
                    .numero { color: #4CAF50; }
                }
                
                &:nth-child(2) {
                    .icono {
                        background: linear-gradient(135deg, rgba(33, 150, 243, 0.1), rgba(33, 150, 243, 0.3));
                        i { color: #2196F3; }
                    }
                    .numero { color: #2196F3; }
                }
                
                &:nth-child(3) {
                    .icono {
                        background: linear-gradient(135deg, rgba(156, 39, 176, 0.1), rgba(156, 39, 176, 0.3));
                        i { color: #9C27B0; }
                    }
                    .numero { color: #9C27B0; }
                }
                
                &:nth-child(4) {
                    .icono {
                        background: linear-gradient(135deg, rgba(255, 152, 0, 0.1), rgba(255, 152, 0, 0.3));
                        i { color: #FF9800; }
                    }
                    .numero { color: #FF9800; }
                }
            }
        }
    }

    // Panel de Filtros mejorado
    .panel-filtros {
        margin-bottom: 4rem;
        padding: 2rem;
        border-radius: 1.5rem;
        background: linear-gradient(145deg, rgba(255,255,255,0.7), rgba(255,255,255,0.9));
        box-shadow: 0 0.8rem 2rem rgba(0, 0, 0, 0.05);
        
        .estadisticas-seccion-titulo {
            color: v.$VerdeUb;
            margin-bottom: 2.5rem;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
            position: relative;
            padding-bottom: 1rem;
            
            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 8rem;
                height: 0.3rem;
                background: v.$Verde;
                border-radius: 1rem;
            }
        }
        
        .form-filtros {
            background: white;
            padding: 2.5rem;
            border-radius: 1.2rem;
            border: none;
            box-shadow: 0px 0px 2px v.$verdelogin;            
            .filtros-fila {
                display: flex;
                flex-wrap: wrap;
                gap: 2.5rem;
                margin-bottom: 2.5rem;
                
                &:last-child {
                    margin-bottom: 0;
                }
                
                .campo-filtro {
                    flex: 1;
                    min-width: 15rem;
                    
                    label {
                        display: block;
                        color: v.$VerdeUb;
                        margin-bottom: 1rem;
                        font-size: 1.5rem;
                        font-weight: 500;
                        letter-spacing: 0.05em;
                    }
                    
                    input, select {
                        width: 100%;
                        padding: 1.2rem 1.5rem;
                        border: 1px solid rgba(v.$Verde, 0.2);
                        border-radius: 0.8rem;
                        background: rgba(255, 255, 255, 0.8);
                        transition: all 0.3s ease;
                        box-shadow: inset 0 0.2rem 0.5rem rgba(0, 0, 0, 0.03);
                        font-size: 1.4rem;
                        color: #333;
                        
                        &:focus {
                            outline: none;
                            border-color: v.$Verde;
                            box-shadow: 0 0 0 0.3rem rgba(v.$Verde, 0.1), 
                                        inset 0 0.2rem 0.5rem rgba(0, 0, 0, 0.03);
                        }
                    }
                    
                    &.campo-boton {
                        display: flex;
                        align-items: flex-end;
                    }
                }
            }
            
            .btn-aplicar-filtros {
                background: linear-gradient(145deg, v.$Verde, v.$VerdeUb);
                color: white;
                border: none;
                padding: 1.2rem 2.5rem;
                border-radius: 0.8rem;
                font-size: 1.5rem;
                font-weight: 600;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
                letter-spacing: 0.05em;
                
                i {
                    margin-right: 1rem;
                    font-size: 1.8rem;
                }
                
                &:hover {
                    background: linear-gradient(145deg, v.$VerdeUb, v.$Verde);
                    transform: translateY(-0.5rem);
                    box-shadow: 0 0.8rem 1.5rem rgba(0, 0, 0, 0.2);
                }
                
                &:active {
                    transform: translateY(0.2rem);
                    box-shadow: 0 0.3rem 0.8rem rgba(0, 0, 0, 0.15);
                }
            }
        }
    }

    // Panel de Resultados mejorado
    .panel-resultados {
        padding: 2rem;
        border-radius: 1.5rem;
        background: linear-gradient(145deg, rgba(255,255,255,0.7), rgba(255,255,255,0.9));
        box-shadow: 0 0.8rem 2rem rgba(0, 0, 0, 0.05);
        margin-bottom: 4rem;
        
        .tabs-container {
            .tabs {
                display: flex;
                margin-bottom: 3rem;
                border-bottom: 1px solid rgba(v.$Verde, 0.2);
                padding: 0 1rem;
                
                .tab-btn {
                    padding: 1.5rem 3rem;
                    background: transparent;
                    border: none;
                    font-size: 1.7rem;
                    font-weight: 500;
                    color: rgba(v.$VerdeUb, 0.6);
                    cursor: pointer;
                    position: relative;
                    transition: all 0.3s ease;
                    letter-spacing: 0.05em;
                    
                    &::after {
                        content: '';
                        position: absolute;
                        bottom: -1px;
                        left: 0;
                        width: 100%;
                        height: 0.3rem;
                        background: transparent;
                        transition: all 0.3s ease;
                        border-radius: 0.3rem 0.3rem 0 0;
                    }
                    
                    &:hover {
                        color: v.$VerdeUb;
                    }
                    
                    &.active {
                        color: v.$VerdeUb;
                        font-weight: 600;
                        
                        &::after {
                            background: linear-gradient(90deg, v.$Verde, v.$VerdeUb);
                            height: 0.4rem;
                        }
                    }
                }
            }
            
            .tab-content {
                .tab-pane {
                    display: none;
                    
                    &.active {
                        display: block;
                        animation: estadisticas-fadeIn 0.5s ease;
                    }
                }
            }
        }
    }

    // Paneles de grficas mejorados
    .paneles-graficas {
        display: flex;
        flex-wrap: wrap;
        gap: 2.5rem;
        margin-bottom: 3rem;
        
        .panel-grafica {
            flex: 1;
            min-width: 30rem;
            padding: 2.5rem;
            border-radius: 1.2rem;
            background: white;
            border: none;        
            box-shadow: 0px 0px 2px v.$verdelogin;    
            .grafica-titulo {
                color: v.$VerdeUb;
                text-align: center;
                margin-bottom: 2.5rem;
                font-size: 1.9rem;
                font-weight: 600;
                position: relative;
                padding-bottom: 1rem;
                
                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 6rem;
                    height: 0.3rem;
                    background: v.$Verde;
                    border-radius: 1rem;
                }
            }
            
            .contenedor-grafica {
                position: relative;
                height: 35rem;
                width: 100%;
                border-radius: 0.8rem;
            }
        }
    }

    // Tablas mejoradas
    .tabla-container {
        margin-bottom: 3.5rem;
        
        .tabla-titulo {
            color: v.$VerdeUb;
            margin-bottom: 2rem;
            font-size: 1.9rem;
            font-weight: 600;
            position: relative;
            padding-bottom: 1rem;
            
            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 6rem;
                height: 0.3rem;
                background: v.$Verde;
                border-radius: 1rem;
            }
        }
        
        .tabla-datos {
            width: 100%;
            border-collapse: separate;
            border-spacing: 0;
            border-radius: 1rem;
            overflow: hidden;
            box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.07);
            
            th, td {
                padding: 1.5rem;
                text-align: left;
            }
            
            th {
                background: linear-gradient(145deg, rgba(v.$Verde, 0.1), rgba(v.$VerdeUb, 0.2));
                color: v.$VerdeUb;
                font-weight: 600;
                text-transform: uppercase;
                font-size: 1.3rem;
                letter-spacing: 0.08em;
                border-bottom: 1px solid rgba(v.$Verde, 0.2);
            }
            
            tr {
                background: white;
                transition: all 0.3s ease;
                
                &:hover td {
                    background: rgba(v.$Verde, 0.05);
                }
                
                &:nth-child(even) {
                    background: rgba(v.$Verde, 0.02);
                }
                
                &:last-child td {
                    border-bottom: none;
                }
            }
            
            td {
                border-bottom: 1px solid rgba(v.$Verde, 0.1);
                font-size: 1.4rem;
                
                &:last-child {
                    font-weight: 600;
                    color: v.$Verde;
                }
            }
        }
        
        .no-datos {
            text-align: center;
            padding: 3rem;
            color: rgba(v.$VerdeUb, 0.6);
            font-style: italic;
            background: white;
            border-radius: 1rem;
            box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.07);
            font-size: 1.5rem;
        }
    }

    // Animaciones especficas para estadsticas
    @keyframes estadisticas-fadeIn {
        from {
            opacity: 0;
            transform: translateY(2rem);
        }
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }

    @keyframes estadisticas-pulse {
        0% {
            transform: scale(1);
            opacity: 1;
        }
        50% {
            transform: scale(1.05);
            opacity: 0.8;
        }
        100% {
            transform: scale(1);
            opacity: 1;
        }
    }

    .estadisticas-pulse {
        animation: estadisticas-pulse 2s infinite ease-in-out;
    }

    // Responsive - ajustado para mantener distribucin en pantallas pequeas
    @media (max-width: 1024px) {
        padding: 0 3%;
        
        .estadisticas-titulo {
            font-size: 2.8rem;
        }
        
        .panel-estadisticas-globales {
            .estadisticas-seccion-titulo {
                font-size: 2.2rem;
            }
            
            .tarjetas-estadisticas {
                .tarjeta-estadistica {
                    min-width: calc(50% - 2rem);
                    
                    .icono {
                        width: 6rem;
                        height: 6rem;
                        
                        i {
                            font-size: 2.2rem;
                        }
                    }
                    
                    .detalles {
                        .numero {
                            font-size: 3rem;
                        }
                    }
                }
            }
        }
        
        .panel-grafica {
            min-width: calc(50% - 2rem);
        }
    }

    @media (max-width: 768px) {
        .estadisticas-titulo {
            font-size: 2.4rem;
        }
        
        .panel-estadisticas-globales {
            padding: 1.5rem;
            
            .estadisticas-seccion-titulo {
                font-size: 2rem;
            }
            
            .tarjetas-estadisticas {
                .tarjeta-estadistica {
                    min-width: 100%;
                    padding: 2rem;
                    
                    .icono {
                        width: 5.5rem;
                        height: 5.5rem;
                    }
                }
            }
        }
        
        .panel-filtros {
            padding: 1.5rem;
            
            .form-filtros {
                padding: 2rem;
                
                .filtros-fila {
                    flex-direction: column;
                    gap: 2rem;
                    
                    .campo-filtro {
                        width: 100%;
                    }
                }
                
                .btn-aplicar-filtros {
                    width: 100%;
                }
            }
        }
        
        .panel-resultados {
            padding: 1.5rem;
        }
        
        .panel-grafica {
            min-width: 100%;
            padding: 2rem;
        }
        
        .tabs {
            .tab-btn {
                padding: 1rem 1.5rem;
                font-size: 1.5rem;
            }
        }
        
        .tabla-container {
            .tabla-datos {
                th, td {
                    padding: 1.2rem;
                }
            }
        }
    }

    @media (max-width: 480px) {
        .estadisticas-titulo {
            font-size: 2rem;
            
            &::after {
                width: 8rem;
            }
        }
        
        .panel-estadisticas-globales {
            .estadisticas-seccion-titulo {
                font-size: 1.8rem;
            }
        }
        
        .panel-filtros .estadisticas-seccion-titulo,
        .panel-grafica .grafica-titulo,
        .tabla-container .tabla-titulo {
            font-size: 1.7rem;
        }
        
        .tabla-container {
            overflow-x: auto;
            
            .tabla-datos {
                min-width: 45rem;
            }
        }
    }
}

